<template lang="html">
  <div class="range-slider" :class="{ disabled }">
    <drag-helper v-bind:disabled="disabled" @dragstart="dragStart" @drag="drag" @dragend="dragEnd">
      <div ref="inner" class="range-slider-inner">
        <input class="range-slider-hidden" type="text" :name="name" :value="actualValue" :disabled="disabled" />
        <div class="range-slider-rail" :style="valueRail"></div>
        <div class="range-slider-fill" :style="valueFill"></div>
        <div class="range-slider-knob" ref="knob" :style="valueKnob">
          <slot name="knob"></slot>
        </div>
      </div>
    </drag-helper>
  </div>
</template>

<script>
import DragHelper from './DragHelper'
import { round } from './utils'

export default {
  props: {
    name: String,
    value: [String, Number],
    disabled: {
      type: Boolean,
      default: false
    },
    min: {
      type: [String, Number],
      default: 0
    },
    max: {
      type: [String, Number],
      default: 100
    },
    step: {
      type: [String, Number],
      default: 1
    },
    height: {
      // 高度
      type: [String, Number],
      default: 16
    },
    // 进度条最外层样式
    railStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 算进度条拖拉显示进度样式
    fillStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 圆角样式
    knobStyle: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },

  data() {
    return {
      actualValue: null,
      dragStartValue: null
    }
  },

  created() {
    const { _min: min, _max: max } = this
    let defaultValue = Number(this.value)

    if (this.value == null || isNaN(defaultValue)) {
      if (min > max) {
        defaultValue = min
      } else {
        defaultValue = (min + max) / 2
      }
    }

    this.actualValue = this.round(defaultValue)
  },

  computed: {
    _min() {
      return Number(this.min)
    },

    _max() {
      return Number(this.max)
    },

    _step() {
      return Number(this.step)
    },

    valuePercent() {
      return ((this.actualValue - this._min) / (this._max - this._min)) * 100
    },
    // 算进度条的高度
    valueRail() {
      return {
        height: this.height + 'px',
        borderRadius: parseInt(this.height) + 20 + 'px',
        ...this.railStyle
      }
    },
    // 算进度条拖拉进度的高度
    valueFill() {
      return {
        width: this.valuePercent + '%',
        height: this.height + 'px',
        borderRadius: parseInt(this.height) + 20 + 'px',
        ...this.fillStyle
      }
    },
    // 算圆的大小高度和宽度
    valueKnob() {
      return {
        left: this.valuePercent + '%',
        width: parseInt(this.height) + 10 + 'px',
        height: parseInt(this.height) + 10 + 'px',
        ...this.knobStyle
      }
    }
  },

  watch: {
    value(newValue) {
      const value = Number(newValue)
      if (newValue != null && !isNaN(value)) {
        this.actualValue = this.round(value)
      }
    },
    min() {
      this.actualValue = this.round(this.actualValue)
    },
    max() {
      this.actualValue = this.round(this.actualValue)
    }
  },

  methods: {
    dragStart(event, offset) {
      this.dragStartValue = this.actualValue
      if (event.target === this.$refs.knob) {
        return
      }
      // If the click is out of knob, move it to mouse position
      this.drag(event, offset)
    },

    drag(event, offset) {
      const { offsetWidth } = this.$refs.inner
      this.actualValue = this.round(this.valueFromBounds(offset.left, offsetWidth))
      this.emitInput(this.actualValue)
    },

    dragEnd(event, offset) {
      const { offsetWidth } = this.$refs.inner
      this.actualValue = this.round(this.valueFromBounds(offset.left, offsetWidth))

      if (this.dragStartValue !== this.actualValue) {
        this.emitChange(this.actualValue)
      }
    },

    emitInput(value) {
      this.$emit('input', value)
    },

    emitChange(value) {
      this.$emit('change', value)
    },

    valueFromBounds(point, width) {
      return (point / width) * (this._max - this._min) + this._min
    },

    round(value) {
      return round(value, this._min, this._max, this._step)
    }
  },

  components: {
    DragHelper
  }
}
</script>

<style lang="scss">
$slider-height: 20px !default;
$slider-width: 130px !default;
$rail-height: 4px !default;
$knob-size: 20px !default;

$rail-color: #1d242d !default;
$rail-fill-color: #f12c4c !default;
$knob-color: #fff !default;

$knob-border: 1px solid #f5f5f5 !default;
$knob-shadow: 1px 1px rgba(0, 0, 0, 0.2) !default;

.range-slider {
  display: inline-block;
  padding: 0 ($knob-size / 2);
  height: $slider-height;
  width: $slider-width;
}

.range-slider.disabled {
  opacity: 0.5;
}

.range-slider-inner {
  display: inline-block;
  position: relative;
  height: 100%;
  width: 100%;
}

.range-slider-rail,
.range-slider-fill {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  height: $rail-height;
  border-radius: $rail-height / 2;
  transform: translateY(-50%);
}

.range-slider-rail {
  width: 100%;
  background: $rail-color;
}

.range-slider-fill {
  background: $rail-fill-color;
}

.range-slider-knob {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  box-sizing: border-box;
  height: $knob-size;
  width: $knob-size;
  border: $knob-border;
  border-radius: 50%;
  background-color: $knob-color;
  box-shadow: $knob-shadow;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.range-slider-hidden {
  display: none;
}
</style>
