<template>
  <div class="comp member">
    <div class="condition">
      <!-- 筛选 -->
      <div class="select">
        <vue-select
          :padding="selectStyle.member_select_padding"
          :color="selectStyle.member_select_text_color"
          :background-color="selectStyle.member_select_bg"
          :font-size="selectStyle.member_select_size"
          :border="selectStyle.member_select_border"
          :border-radius="selectStyle.member_select_radius"
          :icon-color="selectStyle.member_select_icon_color"
          :icon-size="selectStyle.member_select_icon_size"
          :menu-border-radius="selectStyle.member_select_menu_radius"
          :menu-padding="selectStyle.member_select_menu_padding"
          :memu-bg="selectStyle.member_select_memu_bg"
          :active-radius="selectStyle.member_select_active_radius"
          :active-color="selectStyle.member_select_active_color"
          :active-bg="selectStyle.member_select_active_bg"
          :active-font-size="selectStyle.member_select_active_size"
          :active-item-padding="selectStyle.member_select_active_padding"
          :offset-y="selectStyle.member_select_offset_y"
          :offset-x="selectStyle.member_select_offset_x"
          :img-width="selectStyle.member_select_img_width"
          :img-height="selectStyle.member_select_img_height"
          :img-radius="selectStyle.member_select_img_radius"
          :disabled="selectStyle.member_select_disabled"
          :options="levelList"
          :value="params.level"
          @change="onChange"
        >
        </vue-select>
      </div>
      <!--搜索-->
      <div class="search">
        <v-text-field
          :placeholder="$t('UI.MakeMoney_4_Promotion_12')"
          :background-color="commonStyle.member_input_bg"
          class="search_field input_radius"
          outlined
          hide-details
          clearable
          single-line
          solo
          flat
          dense
          v-model="params.nickname"
          @keyup.enter="goPesquisa(params.nickname)"
          @click:clear="onClear"
        >
          <template slot="prepend-inner">
            <icon-font name="icon-Icon_Search" dark="t_text_color_4" size="17"></icon-font>
          </template>
        </v-text-field>
      </div>
    </div>
    <!-- 表格 -->
    <div class="table_member">
      <table-temp :table-head="tableHead" :table-list="tableList" :loadMoreState="loadMoreState" @nextPage="nextPage">
        <template slot="name" slot-scope="{ row }">
          <div class="col-info">
            <div>{{ row.uid }}</div>
            <div>{{ row.nickname }}</div>
          </div>
        </template>
        <template slot="createTime" slot-scope="{ row }">
          <div class="column_flex" v-html="dateTime(row.create_time)"></div>
        </template>
      </table-temp>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { directDetailList } from '@/api/modules/bet.js'
import vueSelect from '@/components/ui/vue-select'
import tableTemp from '@/components/ui/table-temp'
import _common from '@style/views/recomendar.modules.scss'

export default {
  components: {
    vueSelect,
    tableTemp
  },
  data() {
    return {
      commonStyle: _common,
      timer: null, // 定时器名称
      tableList: [], // 数据
      loadMoreState: false, // 是否分页
      params: {
        page: 1, // 页码
        count: 10, // 分页条数
        level: 0, // 等级 0：所有等级 1,2,3: 等级1,2,3
        nickname: '', // 搜索名称
        start_time: '', // 开始时间
        end_time: '' // 结束时间
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 下拉框传入样式 -- 默认
    selectStyle() {
      const style = {
        member_select_padding: _common.member_select_padding,
        member_select_text_color: _common.member_select_text_color,
        member_select_bg: _common.member_select_bg,
        member_select_border: _common.member_select_border,
        member_select_size: _common.member_select_size,
        member_select_radius: _common.member_select_radius,
        member_select_icon_color: _common.member_select_icon_color,
        member_select_icon_size: _common.member_select_icon_size,
        member_select_menu_radius: _common.member_select_menu_radius,
        member_select_menu_padding: _common.member_select_menu_padding,
        member_select_memu_bg: _common.member_select_memu_bg,
        member_select_active_radius: _common.member_select_active_radius,
        member_select_active_color: _common.member_select_active_color,
        member_select_active_bg: _common.member_select_active_bg,
        member_select_active_size: _common.member_select_active_size,
        member_select_active_padding: _common.member_select_active_padding,
        member_select_offset_y: _common.member_select_offset_y,
        member_select_offset_x: _common.member_select_offset_x,
        member_select_img_width: _common.member_select_img_width,
        member_select_img_height: _common.member_select_img_height,
        member_select_img_radius: _common.member_select_img_radius,
        member_select_disabled: !(_common.member_select_disabled == false + '')
      }

      return style
    },
    // 表头
    tableHead() {
      const {
        member_table_size,
        member_table_bold,
        member_table_app_radius,
        member_table_pc_radius,
        member_table_zebra,
        member_table_is_scroll,
        member_table_head_bg,
        member_table_head_radius,
        member_table_head_text_color,
        member_table_head_size,
        member_table_head_bold,
        member_table_head_align,
        member_table_head_margin,
        member_table_head_padding_app,
        member_table_head_padding_pc,
        member_table_head_fixed,
        member_table_hor_bg,
        member_table_hor_app_padding,
        member_table_hor_pc_padding,
        member_table_hor_border_t,
        member_table_hor_border_r,
        member_table_hor_border_b,
        member_table_hor_border_l,
        member_table_hor_margin,
        member_table_hor_color,
        member_table_hor_size,
        member_table_hor_bold,
        member_table_hor_align
      } = _common

      return {
        size: {
          fontSize: member_table_size,
          fontWeight: member_table_bold
        },
        radius: this.isApp ? member_table_app_radius : member_table_pc_radius,
        zebra: member_table_zebra,
        isScroll: !(member_table_is_scroll == false + ''),
        head: {
          bg: member_table_head_bg,
          color: member_table_head_text_color,
          radius: member_table_head_radius,
          size: {
            fontSize: member_table_head_size,
            fontWeight: member_table_head_bold,
            textAlign: member_table_head_align
          },
          margin: member_table_head_margin,
          padding: this.isApp ? member_table_head_padding_app : member_table_head_padding_pc,
          fixed: !(member_table_head_fixed == false + '')
        },
        hor: {
          bg: member_table_hor_bg,
          border_t: member_table_hor_border_t,
          border_r: member_table_hor_border_r,
          border_b: member_table_hor_border_b,
          border_l: member_table_hor_border_l,
          margin: member_table_hor_margin,
          padding: this.isApp ? member_table_hor_app_padding : member_table_hor_pc_padding,
          color: member_table_hor_color,
          size: {
            fontSize: member_table_hor_size,
            fontWeight: member_table_hor_bold,
            textAlign: member_table_hor_align
          }
        },
        columns: [
          // 对应列
          {
            label: `${this.$t('UI.MakeMoney_4_Promotion_13')} / ${this.$t('UI_Apelido')}`,
            align: 'center',
            slot: 'name'
          },
          {
            label: this.$t('UI_Code_HistoryWashingtime'),
            prop: 'create_time',
            align: 'center',
            // width: this.isApp ? '100px' : '150px',
            slot: 'createTime'
          },
          {
            label: this.$t('UI.MakeMoney_1_MakeGold_1'),
            prop: 'level',
            align: 'center',
            width: this.isApp ? '50px' : '150px'
          },
          {
            label: this.$t('UI_Code_Name'),
            prop: 'total_cash',
            align: 'center',
            width: this.isApp ? '106px' : '150px'
          }
          // {
          //     label: this.$t('UI_Directly_under'),
          //     prop: 'team_num',
          //     align: "center",
          // },
        ]
      }
    },
    // 下拉框列表
    levelList() {
      return [
        {
          label: this.$t('UI_PromoteMeberAllLevel'),
          value: 0
        },
        {
          label: this.$t('UI_Member_Level1'),
          value: 1
        },
        {
          label: this.$t('UI_Member_Level2'),
          value: 2
        },
        {
          label: this.$t('UI_Member_Level3'),
          value: 3
        }
      ]
    }
  },
  created() {
    // 初次加载列表，type=0,默认全部
    this.getList()
  },
  methods: {
    /**
     * 加载列表数据
     */
    async getList() {
      let state = true
      const par = {
        ...this.params,
        token: this.user.token,
        language: this.languageType
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const { code, data } = await directDetailList(par).catch(e => e)
      state = false
      if (this.timer) clearTimeout(this.timer) // 清除
      this.$pop.loading().hide()

      if (code != 0) return

      this.loadMoreState = data.length >= this.params.count
      this.tableList = this.tableList.concat(data)
    },
    // 下一页
    nextPage() {
      this.params.page += 1

      this.getList()
    },
    // 下拉列表选择类型
    onChange(v) {
      this.tableList = []
      this.params.page = 1
      this.params.level = v
      this.getList()
    },
    // 搜索名称
    goPesquisa(value = '') {
      this.tableList = []
      this.params.page = 1
      this.params.nickname = value
      this.getList()
    },
    // 清除搜索
    onClear() {
      this.tableList = []
      this.params.page = 1
      this.params.nickname = ''
      this.getList()
    },
    // 处理时间格式
    dateTime(time = '') {
      if (!time) return ['--', '--']

      const value = this.$u.useTimestamp(time, 'yyyy-MM-dd HH:mm') || ''
      const [first, last] = value.split(' ')
      return `
                <span style="font-size: 12px;font-weight: 400;">${first}</span>
                <span style="font-size: 12px;font-weight: 400;">${last}</span>
            `
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.comp.member {
  width: $member_comp_width;
  .condition {
    width: $member_comp_condition_width;
    @include setBox(row, center, center);
    gap: $member_comp_condition_gap;
    .select {
      width: $member_select_width;
    }
    .search {
      width: $member_search_width;
      .search_field {
        width: $member_search_field_width;
        font-size: $member_search_field_size;
        font-weight: $member_search_field_weight;
        ::v-deep {
          .v-input__control {
            min-height: $member_search_field_control_min_height;
            .v-input__slot {
              height: $member_search_field_height !important;
            }
          }
          .v-input__icon .v-icon {
            color: $member_search_field_icon_color !important;
          }
        }
      }
    }
  }
  .table_member {
    width: $member_table_width;
    margin: $member_table_margin;
  }
}
.col-info {
  text-align: center;
  > div:nth-child(1) {
    color: $bonus_col_div1_color;
  }
  > div:nth-child(2) {
    color: $bonus_col_div2_color;
  }
}
</style>
