<template>
  <div class="comp progress_box">
    <div class="progress_item" v-for="(item, i) in progressArr" :key="i">
      <!-- 进度条 -->
      <v-progress-linear
        class="progress_linear"
        :value="item.progress_slider"
        :height="common.progress_box_progress_height"
        :color="common.progress_box_progress_piece"
        :background-color="common.progress_box_progress_bg"
        :rounded="common.progress_box_progress_radius == 'true'"
      />
      <div class="progress_item_data">
        <!-- 金额 -->
        <div class="progress_money line_1">
          <div class="progress_money_text">
            {{ currencySymbol + item.gold }}
          </div>
        </div>
        <!-- 宝箱 -->
        <div class="progress_treasure_chest line_1">
          <div class="treasure_chest">
            <img :src="item.icon" @click="onBox(item)" alt="" />
          </div>
        </div>
        <!-- 提示 -->
        <div class="progress_tip line_1">
          <div class="progress_tip_text">{{ total }}/{{ item.count }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import _common from '@style/views/recomendar.modules.scss'

export default {
  props: {
    // 宝箱列表
    boxList: {
      type: Array,
      default() {
        return {}
      }
    },
    total: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('switchState', ['currencyShow']),
    ...mapState('base', ['isApp', 'systemConfig']),
    // 单位
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 宝箱图片
    goldBox() {
      return {
        0: loadIcon('box_no', false),
        1: loadIcon('box_open', false),
        2: loadIcon('box_collected', false)
      }
    },
    /**
     * 计算进度条
     * 思路：获取宝箱前面4个宝箱等级数组，每个数值区分成一个进度条，计算出每个等级与前面等级相差的数值。
     *      判断当前的值 - 每个等级要达到的值，再进行进度条显示
     * this.total 用户目前充值宝箱等级值
     * this.boxList 获取宝箱前面4个宝箱等级数组
     */
    progressArr() {
      if (this.boxList.length) {
        const newList = this.boxList.map((item, i) => {
          // progress_total: 这个等级进度条的总长度
          const progress_total =
            (item.count || 0) - (this.boxList[i - 1] && this.boxList[i - 1].count ? this.boxList[i - 1].count : 0)
          // progress_slider: 这个等级进度条的占比
          let progress_slider = (this.total || 0) - (item.count || 0)
          // 判断这个等级进度条的占比是否有在这个区间
          // progress_slider >= 0 占满这个进度条 = 1
          // progress_slider < 0 未占满这个进度条 = 计算比例
          if (progress_slider >= 0) {
            progress_slider = 1
          } else {
            progress_slider = Number(progress_total) + Number(progress_slider)
            progress_slider = progress_slider > 0 ? progress_slider / progress_total : 0
          }

          // 判断宝箱状态 0 未完成不可领取，1是完成可领取，2是已领取 // 目前自动领取，状态1可忽略
          const icon = this.goldBox[item.state] || ''

          return {
            ...item,
            progress_total,
            progress_slider: Number((progress_slider * 100).toFixed(2)),
            icon
          }
        })

        return newList
      }

      return []
    },
    // 动态样式
    common() {
      return _common
    }
  },
  methods: {
    // 点击宝箱
    onBox(item = {}) {
      console.log('item', item)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.comp.progress_box {
  width: $progress_box_width;
  @include setBox(row, center, center);

  .progress_item {
    flex-grow: 1;
    flex-shrink: 1;
    @include setBox(row, center, center);

    .progress_linear {
      width: 100%;
    }

    .progress_item_data {
      .progress_money {
        width: 100%;
        margin: $progress_box_progress_money_margin;

        .progress_money_text {
          width: $progress_box_progress_money_text_width;
          font-size: $progress_box_progress_money_text_size;
          font-weight: $progress_box_progress_money_text_bold;
          text-align: $progress_box_progress_money_text_align;
        }
      }

      .progress_treasure_chest {
        width: $progress_box_progress_treasure_chest_width;

        .treasure_chest {
          width: $progress_box_progress_treasure_chest_treasure_chest_width;
          text-align: $progress_box_progress_treasure_chest_treasure_chest_align;

          img {
            width: $progress_box_progress_treasure_chest_treasure_chest_img_width;
          }
        }
      }

      .progress_tip {
        width: $progress_box_progress_tip_width;
        margin: $progress_box_progress_tip_margin;

        .progress_tip_text {
          width: $progress_box_progress_tip_text_width;
          color: $progress_box_progress_tip_text_color;
          font-size: $progress_box_progress_tip_text_size;
          font-weight: $progress_box_progress_tip_text_bold;
          text-align: $progress_box_progress_tip_text_align;
        }
      }
    }
  }
}
</style>
