<template>
  <div class="invite_progress_box" v-if="isShowInite">
    <div class="invite_progress_content" v-html="contentWithLineBreaks(agent_earning_calc_desc)"></div>
    <div class="invite_progress">
      <div class="invite_progress_title">{{ $t('UI_Invite_Progress_Title') }}</div>
      <div class="invite_progress_money">
        <div class="invite_progress_money_text">{{ currencySymbol + ' ' }}{{ getMoney?.gold }}</div>
        <div class="invite_progress_money_icon cursor_point" @click="onOpenRule">
          <img :src="$loadIcon('rull_icon')" alt="" />
        </div>
      </div>
      <div class="invite_progress_text">
        {{ $t('UI_Invite_Progress_Text', { X: getMoney?.invite_num }) }}
      </div>
      <div class="invite_progress_slider">
        <!-- 进度条组件 -->
        <slider
          class="slider"
          :step="sliderStep"
          v-model="sliderValue"
          :knobStyle="knobStyle"
          :railStyle="railStyle"
          :fillStyle="fillStyle"
        >
        </slider>
      </div>
    </div>
    <!-- 帮助弹窗 -->
    <custom-dialog v-model="ruleState">
      <div class="rule_dialog">
        <div class="rule_dialog_head">
          {{ $t('UI.Settings_0_main_5') }}
          <icon-font
            class="rule_dialog_head_icon cursor_point"
            name="icon-close"
            :size="variables.rule_dialog_head_icon_size"
            @click="onClose"
            v-zoom
          >
          </icon-font>
        </div>
        <div class="rule_dialog_content" v-html="contentWithLineBreaks(agent_earning_calc_explain)"></div>
      </div>
    </custom-dialog>
  </div>
</template>

<script>
import slider from './slider/index.vue'
import { getAgentEarningCalcList } from '@/api/modules/agent.js'
import { mapState } from 'vuex'
import variables from '@style/views/recomendar.modules.scss'
import { getCacheIcon } from '@/assets/imgCache.js'
export default {
  data() {
    return {
      variables,
      isShowInite: false,
      sliderValue: 1, // 进度条选中值
      sliderStep: 1, // 进度条离散值
      agent_earning_calc_desc: '', // 文本
      agent_earning_calc_explain: '', // 帮助文本提示
      levels: [], // 进度条挡位
      ruleState: false // 弹窗开关
    }
  },
  components: {
    slider
  },
  computed: {
    ...mapState('switchState', ['currencyShow']),
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    // 处理金额类型
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 进度条拖动显示赠送金币
    getMoney() {
      // 取整，这边是用四舍五入
      const inx = Math.round(this.sliderValue / this.sliderStep)
      if (this.levels.length >= inx) return this.levels[inx]
      return ''
    },
    knobStyle() {
      return {
        background: this.variables.rule_progress_knobStyle_bg,
        border: this.variables.rule_progress_knobStyle_border,
        width: this.variables.rule_progress_knobStyle_width,
        height: this.variables.rule_progress_knobStyle_height
      }
    },
    railStyle() {
      return {
        height: this.variables.rule_progress_railStyle_height,
        borderRadius: this.variables.rule_progress_railStyle_border_radius,
        background: this.variables.rule_progress_railStyle_bg
      }
    },
    fillStyle() {
      return {
        height: this.variables.rule_progress_fillStyle_height,
        borderRadius: this.variables.rule_progress_fillStyle_border_radius,
        background: this.variables.rule_progress_fillStyle_bg
      }
    }
  },
  created() {
    // 进度条列表数据
    this.getAgentEarningCalcListFn()
  },
  methods: {
    // 进度条列表数据
    async getAgentEarningCalcListFn() {
      const params = { token: this.user.token, language: this.languageType }
      const { code = 0, data } = await getAgentEarningCalcList(params).catch(e => e)
      if (code == 0 && !!data) {
        this.isShowInite = true
        const { agent_earning_calc_desc = '', levels = [], agent_earning_calc_explain = '' } = data
        this.agent_earning_calc_desc = agent_earning_calc_desc
        this.agent_earning_calc_explain = agent_earning_calc_explain
        this.levels = levels
        // 间距大小，100/数据个数
        this.sliderStep = 100 / (levels.length - 1)
      } else {
        this.isShowInite = false
      }
    },
    // /n 换行
    contentWithLineBreaks(text) {
      if (!text) return ''
      return text.replace(/\n/g, '<br>')
    },
    // 弹窗关闭
    onClose() {
      this.ruleState = false
    },
    // 打开弹窗
    onOpenRule() {
      this.ruleState = true
    },
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.invite_progress_box {
  .invite_progress_content {
    width: $invite_progress_content_width;
    font-size: $invite_progress_content_font_size;
    font-weight: $invite_progress_content_font_weight;
    margin: $invite_progress_content_margin;
    color: $invite_progress_content_color;
    text-align: $invite_progress_content_align;
  }
  .invite_progress {
    background: $invite_progress_bg;
    border-radius: $invite_progress_border_radius;
    .invite_progress_title {
      font-weight: $invite_progress_title_font_weight;
      font-size: $invite_progress_title_font_size;
      color: $invite_progress_title_color;
      text-align: $invite_progress_title_text_align;
    }
    .invite_progress_money {
      margin: $invite_progress_money_margin;
      font-weight: $invite_progress_money_font_weight;
      font-size: $invite_progress_money_font_size;
      text-align: $invite_progress_money_text_align;
      @include setBox(row, center, center);
      .invite_progress_money_text {
        color: $invite_progress_money_text_color;
      }
      .invite_progress_money_icon {
        margin: $invite_progress_money_icon_margin;
        // color: $invite_progress_money_icon_color;
        img {
          width: $invite_progress_money_icon_width;
          height: $invite_progress_money_icon_height;
        }
      }
    }
    .invite_progress_text {
      margin: $invite_progress_text_margin;
      font-weight: $invite_progress_text_font_weight;
      font-size: $invite_progress_text_font_size;
      color: $invite_progress_text_color;
      text-align: $invite_progress_text_text-align;
    }
    .invite_progress_slider {
      margin: $invite_progress_slider_margin;
      @include setBox(row, center, center);
      .slider {
        /* overwrite slider styles */
        width: $invite_progress_slider_width;
      }
    }
  }
}
.rule_dialog {
  width: $invite_progress_rule_dialog_width;
  min-height: $invite_progress_rule_dialog_min_height;
  border-radius: $invite_progress_rule_dialog_border_radius;
  background: $invite_progress_rule_dialog_bg;
  .rule_dialog_head {
    background: $invite_progress_rule_dialog_head_bg;
    border-top-left-radius: $invite_progress_rule_dialog_head_border_left_radius;
    border-top-right-radius: $invite_progress_rule_dialog_head_border_right_radius;
    height: $invite_progress_rule_dialog_height;
    width: $invite_progress_rule_dialog_width;
    line-height: $invite_progress_rule_dialog_height;
    text-align: $invite_progress_rule_dialog_text_align;
    position: relative;
    color: $invite_progress_rule_dialog_color;
    font-weight: $invite_progress_rule_dialog_font_weight;
    font-size: $invite_progress_rule_dialog_font_size;
    .rule_dialog_head_icon {
      position: absolute;
      right: $invite_progress_rule_dialog_head_right;
      top: $invite_progress_rule_dialog_head_top;
      transform: $invite_progress_rule_dialog_head_transform;
      color: $invite_progress_rule_dialog_head_color;
    }
  }
  .rule_dialog_content {
    padding: $invite_progress_rule_dialog_content_padding;
    font-weight: $invite_progress_rule_dialog_content_font_weight;
    font-size: $invite_progress_rule_dialog_content_font_size;
    color: $invite_progress_rule_dialog_content_color;
  }
}

@media (max-width: 1080px) {
  .invite_progress_box {
    padding: $invite_progress_box_app_padding;
    width: 100%;
    .invite_progress {
      padding: $invite_progress_app_padding;
    }
  }
}
@media (min-width: 1080px) {
  .invite_progress_box {
    padding: $invite_progress_box_pc_padding;
    width: 100%;
    .invite_progress {
      padding: $invite_progress_pc_padding;
    }
    .rule_dialog {
      width: $rule_progress_rule_dialog_width_pc;
    }
  }
}
</style>
