<template>
  <div class="comp Referir">
    <div class="swiper_tab">
      <swiper-tab @value="emitChangeDate" />
    </div>
    <div class="select_time">
      <date-picker
        :time="daterange"
        :size="datePickerSizeRadius.size"
        :radius="datePickerSizeRadius.radius"
        nudge-bottom="16"
        @change="emitChangeDate"
      />
      <v-btn v-zoom class="search_btn cursor_point btn_radius" color="t_btn_host_color" @click="onSearchBtn">
        {{ $t('General.General_12') }}
      </v-btn>
    </div>
    <div class="referir_tip text_center">
      {{ $t('UI_Tab_Refer_Tip') }}
    </div>
    <div class="referir_data">
      <div v-for="(item, i) in referirList" :key="i" class="referir_data_item radius">
        <span class="lt">{{ $t(item.name) }}</span>
        <span class="rt">{{ item.value }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import swiperTab from './ui/swiper-tab.vue'
import DatePicker from '@/components/ui/datepicker/date-picker'
import { agentReport, spreadWay } from '@/api/modules/agent.js'
import _common from '@style/views/recomendar.modules.scss'

export default {
  components: {
    swiperTab,
    DatePicker
  },
  data() {
    return {
      daterange: [], // 时间
      referirData: {
        // 数据
        total_cash: {
          // 总佣金
          name: '',
          value: 0
        },
        team_num: {
          // 团队成员人数
          name: '',
          value: 0
        },
        cash_withdraw: {
          // 佣金提取
          name: '',
          value: 0
        },
        sub_cash: {
          // 投注佣金
          name: '',
          value: 0
        },
        sub_first_recharge: {
          // 邀请奖励
          name: '',
          value: 0
        },
        sub_total_users: {
          // 新充值玩家
          name: '',
          value: 0
        },
        direct_num: {
          // 直属会员人数
          name: '',
          value: 0
        },
        cash_transfer: {
          // 佣金转移
          name: '',
          value: 0
        },
        share_box: {
          // 邀请成就
          name: '',
          value: 0
        },
        sub_bet_amount: {
          // 总下注额
          name: '',
          value: 0
        },
        sub_recharge_amount: {
          // 充值总额
          name: '',
          value: 0
        }
      },
      agent_share_reward_switch: 0 // Invitation conquest 显隐开关
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapGetters('base', ['isAgentDateSwitch']),
    // 数据多语言
    languageArr() {
      return {
        // 总佣金
        total_cash: 'UI_Tab_Refer_all_money',
        // 团队成员人数
        team_num: 'UI_Tab_Refer_Team_member',
        // 佣金提取
        cash_withdraw: 'UI_Tab_Refer_Withdrawal',
        // 投注佣金
        sub_cash: 'UI_Tab_Refer_Commission',
        // 邀请奖励
        sub_first_recharge: 'UI_Tab_Refer_Bonus',
        // 新充值玩家
        sub_total_users: 'UI_Tab_Refer_players',
        // 直属会员人数
        direct_num: 'UI_Tab_Refer_direct_members',
        // 佣金转移
        cash_transfer: 'UI_Tab_Refer_Transfer',
        // 邀请成就
        share_box: 'UI_Tab_Refer_Conquest',
        // 总下注额
        sub_bet_amount: 'UI_Tab_Refer_Sub_Bet_Amount',
        // 充值总额
        sub_recharge_amount: 'UI_Tab_Refer_Sub_Recharge_Amount'
      }
    },
    // 循环数组
    referirList() {
      let referirData = JSON.parse(JSON.stringify(this.referirData))
      if (this.agent_share_reward_switch == 0) {
        delete referirData.share_box
      }
      // 代理团队数据开关
      if (!this.isAgentDateSwitch) {
        delete referirData.sub_bet_amount
        delete referirData.sub_recharge_amount
      }
      return Object.values(referirData || {})
    },
    // 时间选择框大小
    datePickerSizeRadius() {
      return {
        size: this.isApp ? _common.referir_date_picker_size_app : _common.referir_date_picker_size_pc,
        radius: parseInt(this.isApp ? _common.referir_date_picker_radius_app : _common.referir_date_picker_radius_pc)
      }
    }
  },
  methods: {
    // 获取快捷选择时间, 选择日期
    emitChangeDate(value = []) {
      this.daterange = value
      this.getData()
    },
    // 搜索
    onSearchBtn() {
      this.getData()
    },
    // 获取数据
    async getData() {
      let state = true
      const params = {
        token: this.user.token,
        start_date: this.daterange[0] ? this.daterange[0] : '',
        end_date: this.daterange[1] ? this.daterange[1] : '',
        language: this.languageType
      }
      const params1 = { token: this.user.token, language: this.languageType }
      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const [spreadRes, agentRes] = await Promise.all([
        spreadWay(params1).catch(e => e),
        agentReport(params).catch(e => e)
      ])
      const spreadData = spreadRes.data && this.$u.lodash.isObject(spreadRes.data) ? spreadRes.data : {}
      const { agent_share_reward_switch } = spreadData

      this.agent_share_reward_switch = agent_share_reward_switch
      state = false
      if (this.timer) clearTimeout(this.timer)
      this.$pop.loading().hide()
      if (!agentRes || agentRes.code != 0) return

      const resData = agentRes.data && this.$u.lodash.isObject(agentRes.data) ? agentRes.data : {}
      for (const key in this.referirData) {
        this.referirData[key].name = this.languageArr[key]
        this.referirData[key].value = resData[key]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.comp.Referir {
  .radius {
    border-radius: $referir_radius_app;
  }
  .swiper_tab {
    margin: $referir_swiper_tab_margin;
  }

  .select_time {
    margin: $referir_select_time_margin;
    @include setBox(row, center, center);

    .search_btn {
      width: auto;
      height: $referir_search_btn_app_height;
      margin: $referir_search_btn_app_margin;
    }
  }

  .referir_tip {
    font-size: $referir_referir_tip_size;
    font-weight: $referir_referir_tip_bold;
    color: $referir_referir_tip_color;
    margin: $referir_referir_tip_margin;
  }

  .referir_data {
    width: 100%;
    @include setBox(column, center, center);
    margin: $referir_referir_data_app_margin;

    .referir_data_item {
      width: 100%;
      @include setBox(row, space-between, center);
      padding: $referir_referir_data_item_app_padding;
      background: $referir_referir_data_item_app_bg;
      margin: $referir_referir_data_item_app_margin;

      &:last-child {
        margin-bottom: 0;
      }

      .lt {
        font-size: $referir_referir_data_item_lt_app_size;
        font-weight: $referir_referir_data_item_lt_app_bold;
        color: $referir_referir_data_item_lt_app_color;
      }

      .rt {
        font-size: $referir_referir_data_item_rt_app_size;
        font-weight: $referir_referir_data_item_rt_app_bold;
        color: $referir_referir_data_item_rt_app_color;
        margin: $referir_referir_data_item_rt_app_margin;
      }
    }
  }
}

// pc样式
@include breakpoint-only-up() {
  .comp.Referir {
    .radius {
      border-radius: $referir_radius_pc;
    }
    .select_time {
      .search_btn {
        height: $referir_search_btn_pc_height;
        margin: $referir_search_btn_pc_margin;
      }
    }
    .referir_data {
      margin: $referir_referir_data_pc_margin;
      .referir_data_item {
        padding: $referir_referir_data_item_pc_padding;
        background: $referir_referir_data_item_pc_bg;
        margin: $referir_referir_data_item_pc_margin;
        .lt {
          font-size: $referir_referir_data_item_lt_pc_size;
          font-weight: $referir_referir_data_item_lt_pc_bold;
          color: $referir_referir_data_item_lt_pc_color;
        }
        .rt {
          font-size: $referir_referir_data_item_rt_pc_size;
          font-weight: $referir_referir_data_item_rt_pc_bold;
          color: $referir_referir_data_item_rt_pc_color;
          margin: $referir_referir_data_item_rt_pc_margin;
        }
      }
    }
  }
}
</style>
