<template>
  <div class="comp swiperTab">
    <swiper class="category-scrollbar" ref="swiperRef" :options="swiperOptionsInfo">
      <swiper-slide
        v-for="(item, i) in tabList"
        :key="i"
        class="game-category-item"
        :class="{ 'game-category-item--active': activeIndex === i }"
        @click.native="change(i, item.value)"
      >
        <span class="line_1">{{ item.name }}</span>
      </swiper-slide>
      <div class="swiper-scrollbar" :class="{ 'd-block': isApp }" slot="scrollbar"></div>
    </swiper>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import _common from '@style/views/recomendar.modules.scss'
import 'swiper/css/swiper.css'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      swiperOptionsInfo: {
        // swiper 初始化数据
        freeMode: true,
        freeModeMomentumRatio: 0.5,
        resistanceRatio: 0.1,
        slidesPerView: 'auto',
        spaceBetween: parseInt(_common.swiper_tab_margin),
        scrollbar: {
          el: '.swiper-scrollbar',
          hide: true,
          draggable: true
        }
      },
      activeIndex: 0 // 当前 tab 选中索引
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    // tab list
    tabList() {
      return [
        // 全部
        // {
        //     name: this.$t('UI_ALL'),
        //     value: ['', '']
        // },
        // 今天
        {
          name: this.$t('UI_Tab_Refer_Today'),
          value: [this.$u.datetime.gettoday(), this.$u.datetime.gettoday()]
        },
        // 昨天
        {
          name: this.$t('UI_Tab_Refer_Yesterday'),
          value: [this.$u.datetime.getYesterday(), this.$u.datetime.getYesterday()]
        },
        // 本周
        {
          name: this.$t('UI_Tab_Refer_This_Week'),
          value: this.$u.datetime.getCurrentWeekDate()
        },
        // 上周
        {
          name: this.$t('UI_Tab_Refer_Last_Week'),
          value: this.$u.datetime.getUpperWeekDate()
        },
        // 上月
        {
          name: this.$t('UI_Tab_Refer_Last_Month'),
          value: this.$u.datetime.prevMonth()
        }
      ]
    }
  },
  watch: {
    isApp() {
      this.bindSwiperRect()
    }
  },
  mounted() {
    this.bindSwiperRect()

    // 初始化的时间
    this.$emit('value', this.tabList[this.activeIndex].value)
  },
  methods: {
    // swiper 样式初始化
    bindSwiperRect() {
      const swiperInstance = this.$refs['swiperRef'].$swiper
      this.swiperInstance = swiperInstance
      this.swiperRect = swiperInstance.$el[0].getBoundingClientRect()
    },
    // swiper 滚动条显示
    swipeScroll(i) {
      const { swiperInstance = {}, swiperRect = {} } = this
      const swiperWidth = swiperRect.width || 0
      const slide = swiperInstance.slides[i]
      const slideLeft = slide.offsetLeft || 0
      const slideWidth = slide.clientWidth || 0
      const slideRight = slideLeft + slideWidth
      const slideRect = slide.getBoundingClientRect()
      swiperInstance.setTransition(300)
      // 视区左侧区域
      if (slideRect.left - swiperRect.left < 0) return swiperInstance.setTranslate(-slideLeft)
      // 视区右侧区域
      if (slideRect.right - swiperRect.right > 0) return swiperInstance.setTranslate(-(slideRight - swiperWidth))
    },
    // 切换tab
    change(i, time = []) {
      if (this.activeIndex === i) return
      this.swipeScroll(i)
      this.activeIndex = i
      this.$emit('value', time)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.comp.swiperTab {
  .game-category-item {
    @include setBox(row, center, center);
    width: $swiper_tab_item_width;
    background: $swiper_tab_item_bg;
    padding: $swiper_tab_item_padding;
    border-radius: $swiper_tab_item_radius;
    cursor: pointer;
    user-select: none;

    span {
      min-width: $swiper_tab_item_text_min_width;
      font-size: $swiper_tab_item_text_size;
      font-weight: $swiper_tab_item_text_bold;
      color: $swiper_tab_item_text_color;
      text-align: $swiper_tab_item_text_align;
      white-space: nowrap;
    }
  }

  .game-category-item--active {
    background: $swiper_tab_item_active_bg;
    box-shadow: $swiper_tab_item_active_box_shadow;
    &:hover {
      @include setFilter($btn_hover_filter, $btn_hover_filter_type);
    }
    span {
      font-size: $swiper_tab_item_active_text_size;
      font-weight: $swiper_tab_item_active_text_bold;
      color: $swiper_tab_item_active_text_color;
    }
  }

  ::v-deep .category-scrollbar {
    padding: $swiper_tab_scrollbar_padding;

    .swiper-scrollbar {
      display: none;
      height: $swiper_tab_scrollbar_height;
      background: $swiper_tab_scrollbar_bg;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .swiper-scrollbar-drag {
      background: $swiper_tab_scrollbar_drag_bg;
    }
  }
}
</style>
