<template>
  <div class="comp bonus">
    <vue-select
      class="vue_select"
      :padding="selectStyle.bonus_select_padding"
      :color="selectStyle.bonus_select_text_color"
      :background-color="selectStyle.bonus_select_bg"
      :font-size="selectStyle.bonus_select_size"
      :border="selectStyle.bonus_select_border"
      :border-radius="selectStyle.bonus_select_radius"
      :icon-color="selectStyle.bonus_select_icon_color"
      :icon-size="selectStyle.bonus_select_icon_size"
      :menu-border-radius="selectStyle.bonus_select_menu_radius"
      :menu-padding="selectStyle.bonus_select_menu_padding"
      :memu-bg="selectStyle.bonus_select_memu_bg"
      :active-radius="selectStyle.bonus_select_active_radius"
      :active-color="selectStyle.bonus_select_active_color"
      :active-bg="selectStyle.bonus_select_active_bg"
      :active-font-size="selectStyle.bonus_select_active_size"
      :active-item-padding="selectStyle.bonus_select_active_padding"
      :offset-y="selectStyle.bonus_select_offset_y"
      :offset-x="selectStyle.bonus_select_offset_x"
      :img-width="selectStyle.bonus_select_img_width"
      :img-height="selectStyle.bonus_select_img_height"
      :img-radius="selectStyle.bonus_select_img_radius"
      :disabled="selectStyle.bonus_select_disabled"
      :options="goldTypes"
      :value="params.type"
      @change="onChange"
    >
    </vue-select>

    <div class="table_bonus">
      <table-temp :table-head="headList" :table-list="tableList" :load-more-state="loadMoreState" @nextPage="nextPage">
        <template slot="createTime" slot-scope="{ row }">
          <div class="table_time" v-html="handleTime(row.create_time)"></div>
        </template>
        <template slot="info" slot-scope="{ row }">
          <div class="col-info">
            <div>{{ row.change_gold }}</div>
            <div class="break_word">{{ row.type_name }}</div>
          </div>
        </template>
      </table-temp>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getAgentBonusRecords } from '@/api/modules/agent.js'
import vueSelect from '@/components/ui/vue-select'
import tableTemp from '@/components/ui/table-temp'
import _common from '@style/views/recomendar.modules.scss'

export default {
  components: {
    vueSelect,
    tableTemp
  },
  data() {
    return {
      timer: null, // 定时器名称
      tableList: [], // 表格列表
      loadMoreState: false, // 是否有下一页
      params: {
        type: 0,
        page: 1,
        limit: 10
      },
      goldTypes: [] // 下拉框
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 下拉框传入样式 -- 默认
    selectStyle() {
      const style = {
        bonus_select_padding: _common.bonus_select_padding,
        bonus_select_text_color: _common.bonus_select_text_color,
        bonus_select_bg: _common.bonus_select_bg,
        bonus_select_border: _common.bonus_select_border,
        bonus_select_size: _common.bonus_select_size,
        bonus_select_radius: _common.bonus_select_radius,
        bonus_select_icon_color: _common.bonus_select_icon_color,
        bonus_select_icon_size: _common.bonus_select_icon_size,
        bonus_select_menu_radius: _common.bonus_select_menu_radius,
        bonus_select_menu_padding: _common.bonus_select_menu_padding,
        bonus_select_memu_bg: _common.bonus_select_memu_bg,
        bonus_select_active_radius: _common.bonus_select_active_radius,
        bonus_select_active_color: _common.bonus_select_active_color,
        bonus_select_active_bg: _common.bonus_select_active_bg,
        bonus_select_active_size: _common.bonus_select_active_size,
        bonus_select_active_padding: _common.bonus_select_active_padding,
        bonus_select_offset_y: _common.bonus_select_offset_y,
        bonus_select_offset_x: _common.bonus_select_offset_x,
        bonus_select_img_width: _common.bonus_select_img_width,
        bonus_select_img_height: _common.bonus_select_img_height,
        bonus_select_img_radius: _common.bonus_select_img_radius,
        bonus_select_disabled: !(_common.bonus_select_disabled == false + '')
      }

      return style
    },
    // 表头
    headList() {
      const {
        bonus_table_size,
        bonus_table_bold,
        bonus_table_app_radius,
        bonus_table_pc_radius,
        bonus_table_zebra,
        bonus_table_is_scroll,
        bonus_table_head_bg,
        bonus_table_head_radius,
        bonus_table_head_text_color,
        bonus_table_head_size,
        bonus_table_head_bold,
        bonus_table_head_align,
        bonus_table_head_margin,
        bonus_table_head_padding_app,
        bonus_table_head_padding_pc,
        bonus_table_head_fixed,
        bonus_table_hor_bg,
        bonus_table_hor_app_padding,
        bonus_table_hor_pc_padding,
        bonus_table_hor_border_t,
        bonus_table_hor_border_r,
        bonus_table_hor_border_b,
        bonus_table_hor_border_l,
        bonus_table_hor_margin,
        bonus_table_hor_color,
        bonus_table_hor_size,
        bonus_table_hor_bold,
        bonus_table_hor_align
      } = _common
      return {
        size: {
          fontSize: bonus_table_size,
          fontWeight: bonus_table_bold
        },
        radius: this.isApp ? bonus_table_app_radius : bonus_table_pc_radius,
        zebra: bonus_table_zebra,
        isScroll: !(bonus_table_is_scroll == false + ''),
        head: {
          bg: bonus_table_head_bg,
          color: bonus_table_head_text_color,
          radius: bonus_table_head_radius,
          size: {
            fontSize: bonus_table_head_size,
            fontWeight: bonus_table_head_bold,
            textAlign: bonus_table_head_align
          },
          margin: bonus_table_head_margin,
          padding: this.isApp ? bonus_table_head_padding_app : bonus_table_head_padding_pc,
          fixed: !(bonus_table_head_fixed == false + '')
        },
        hor: {
          bg: bonus_table_hor_bg,
          border_t: bonus_table_hor_border_t,
          border_r: bonus_table_hor_border_r,
          border_b: bonus_table_hor_border_b,
          border_l: bonus_table_hor_border_l,
          margin: bonus_table_hor_margin,
          padding: this.isApp ? bonus_table_hor_app_padding : bonus_table_hor_pc_padding,
          color: bonus_table_hor_color,
          size: {
            fontSize: bonus_table_hor_size,
            fontWeight: bonus_table_hor_bold,
            textAlign: bonus_table_hor_align
          }
        },
        columns: [
          {
            label: this.$t('UI.MakeMoney_4_Promotion_13'),
            prop: 'c_id',
            align: 'center',
            width: this.isApp ? '80px' : '160px'
          },
          {
            label: this.$t('UI.MakeMoney_5_Withdrawal_3'),
            prop: 'create_time',
            align: 'center',
            slot: 'createTime'
          },
          {
            label: `${this.$t('UI_Promote_TitleType')} / ${this.$t('UI_Code_Bonus')}`,
            slot: 'info',
            align: 'center'
          }
        ]
      }
    }
  },
  created() {
    this.getList()
  },
  methods: {
    // 获取列表
    async getList() {
      let state = true
      const params = {
        token: this.user.token,
        language: this.languageType,
        ...this.params
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)

      const { code, data = {}, msg = '' } = await getAgentBonusRecords(params).catch(e => e)
      state = false
      if (this.timer) clearTimeout(this.timer) // 清除
      this.$pop.loading().hide()
      if (code != 0) return this.$pop.message().show({ content: msg })

      const dataList = data.lists && this.$u.lodash.isObject(data.lists) ? data.lists : {}
      const list = dataList.data && Array.isArray(dataList.data) ? dataList.data : []

      if (this.goldTypes.length == 0) {
        const { types = {} } = data
        let arr = [{ label: this.$t('UI_ALL'), value: 0 }]

        Object.keys(types).forEach(key => {
          arr.push({ label: types[key], value: key })
        })

        this.goldTypes = arr
      }
      this.loadMoreState = list.length >= this.params.limit

      this.tableList = this.tableList.concat(list)
    },
    // 下一页
    nextPage() {
      this.params.page += 1
      this.getList()
    },
    // 选择类型
    onChange(v) {
      this.tableList = []
      this.params.page = 1
      this.params.type = v
      this.getList()
    },
    // 处理时间
    handleTime(time = '') {
      if (!time) return []
      const value = this.$u.useTimestamp(time, 'yyyy-MM-dd HH:mm') || ''
      const [first, last] = value.split(' ')
      return `<span>${first}</span><span>${last}</span>`
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.comp.bonus {
  .vue_select {
    margin: $bonus_vue_select_margin;
  }

  .table_bonus {
    width: $bonus_table_width;
    margin: $bonus_table_margin;

    .table_time {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}

.col-info {
  text-align: center;

  & > div:nth-child(1) {
    color: $bonus_col_div1_color;
  }

  & > div:nth-child(2) {
    color: $bonus_col_div2_color;
  }
}
</style>
