<template>
  <div class="comp date_box">
    <div class="date_box_tab">
      <swiper-tab @value="onChangeDate" />
    </div>
    <div class="date_box_time">
      <date-picker :time="params.time" size="small" :radius="4" nudge-bottom="16" @change="onChangeDate" />
      <v-btn v-zoom class="search_btn cursor_point btn_radius" color="t_btn_host_color" @click="onSearch">
        {{ $t('General.General_12') }}
      </v-btn>
    </div>
    <div class="referir_tip text_center">
      {{ $t('UI_Recomendar_Direct_Member_Tip_Text') }}
    </div>
    <!-- 表格 -->
    <div class="date_box_table">
      <table-temp :table-head="tableHead" :table-list="tableList" :loadMoreState="loadMoreState" @nextPage="onNextPage">
        <template slot="name" slot-scope="{ row }">
          <div class="table_col_name">
            <div class="uid_text">{{ row.uid }}</div>
            <div class="name_text">
              <auto-font uuid="dateBoxName" maxFont="12" :text="row.nickname" />
            </div>
          </div>
        </template>
      </table-temp>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import swiperTab from './ui/swiper-tab.vue'
import DatePicker from '@/components/ui/datepicker/date-picker'
import tableTemp from '@/components/ui/table-temp'
import { getAgentDirectList } from '@/api/modules/agent.js'
import _color from '@style/_global/_color.module.scss'

export default {
  components: {
    swiperTab,
    DatePicker,
    tableTemp
  },
  data() {
    return {
      // 列表数据
      tableList: [],
      // 是否有分页
      loadMoreState: false,
      // 参数
      params: {
        page: 1,
        limit: 10,
        time: []
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 表头
    tableHead() {
      return {
        size: {
          fontSize: '12px',
          fontWeight: '400'
        },
        radius: this.isApp ? '4px' : '8px',
        head: {
          bg: _color.table_head_bg,
          color: _color.text_color_1,
          radius: '4px',
          size: {
            fontSize: '12px',
            fontWeight: '400',
            textAlign: 'left'
          },
          padding: '10px'
        },
        hor: {
          border_b: _color.table_border,
          padding: this.isApp ? '18px 0px' : '22px 0px',
          size: {
            fontSize: '12px',
            fontWeight: '600',
            textAlign: 'left'
          }
        },
        columns: [
          {
            label: this.$t('UI_Recomendar_Id_Name'),
            prop: 'uid',
            align: 'center',
            slot: 'name'
          },
          {
            label: this.$t('UI_Recomendar_Direct_Member'),
            prop: 'team_num',
            align: 'center'
          },
          {
            label: this.$t('UI_Recomendar_Direct_Member_Interval'),
            prop: 'recharge_amount',
            align: 'center'
          },
          {
            label: this.$t('UI_Recomendar_Direct_Member_Bet'),
            prop: 'bet_gold',
            align: 'center'
          }
        ]
      }
    }
  },
  methods: {
    // 获取列表
    async getList() {
      let state = true
      const { time = [], ...rest } = this.params
      const par = {
        token: this.user?.token,
        language: this.languageType,
        start_date: time && time[0] ? time[0] : '',
        end_date: time && time[1] ? time[1] : '',
        ...rest
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const res = await getAgentDirectList(par).catch(e => e)
      state = false
      if (this.timer) clearTimeout(this.timer)
      this.$pop.loading().hide()
      if (res.code != 0) return

      const { list = {} } = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      const { data = [] } = list && this.$u.lodash.isObject(list) ? list : {}
      const resList = Array.isArray(data) ? data : []
      this.tableList = this.tableList.concat(resList)
      this.loadMoreState = resList.length >= this.params.limit
    },
    // 获取快捷选择时间, 选择日期
    onChangeDate(value = []) {
      this.tableList = []
      this.params.page = 1
      this.params.time = value
      this.getList()
    },
    // 搜索
    onSearch() {
      this.tableList = []
      this.params.page = 1
      this.getList()
    },
    // 下一页
    onNextPage() {
      this.params.page++
      this.getList()
    }
  }
}
</script>

<style lang="scss" scoped>
.comp.date_box {
  width: 100%;
  .date_box_time {
    width: 100%;
    margin: 12px 0 0 0;
    @include setBox(row, flex-start, center);
    gap: 0 12px;
  }
  .referir_tip {
    font-size: 12px;
    font-weight: 400;
    color: $text_color_1;
    margin: 16px 0 0 0;
  }
  .date_box_table {
    width: 100%;
    margin: 16px 0 0 0;
    .table_col_name {
      width: 100%;
      @include setBox(column, center, center);
      .uid_text {
        font-size: 12px;
        font-weight: 400;
        color: $white;
        line-height: 1;
      }
      .name_text {
        width: 100%;
        height: 16px;
        color: $text_color_1;
      }
    }
  }
}
</style>
