<template>
  <v-container class="page recomendar" ref="boxIds" app>
    <div class="head_tab">
      <div class="tabs" v-if="rendering">
        <v-tabs
          class="tab_tify"
          fixed-tabs
          v-model="tabIndex"
          :slider-color="common.recomendar_tab_border_color"
          @change="onChange"
        >
          <v-tab v-for="(item, index) in tabList" :key="index" :ripple="false" class="tab_item">
            <div style="width: 100%" class="tab_item_text">
              {{ item.name }}
            </div>
          </v-tab>
        </v-tabs>
        <v-divider></v-divider>
      </div>
    </div>

    <template v-if="tabList && tabList.length > 0 && rendering">
      <div class="conent">
        <v-tabs-items v-model="tabIndex" touchless>
          <v-tab-item>
            <lnvite-comp />
          </v-tab-item>
          <v-tab-item>
            <referir-comp />
          </v-tab-item>
          <v-tab-item>
            <bonus-comp />
          </v-tab-item>
          <v-tab-item>
            <date-comp />
          </v-tab-item>
          <v-tab-item>
            <member-comp />
          </v-tab-item>
        </v-tabs-items>
      </div>
    </template>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import lnviteComp from './lnvite'
import BonusComp from './Bonus'
import MemberComp from './Member'
import ReferirComp from './Referir'
import DateComp from './Date'
import _common from '@style/views/recomendar.modules.scss'

export default {
  name: 'recomendar',
  components: {
    lnviteComp,
    BonusComp,
    MemberComp,
    ReferirComp,
    DateComp
  },
  mixins: [ContainerMixin],
  data() {
    return {
      common: _common,
      tabIndex: 0, // tab 切换的索引
      rendering: true // tab 重新渲染
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType', 'systemConfig', 'isApp']),
    ...mapGetters('base', ['isAgentDateSwitch']),
    // tab 数组
    tabList() {
      return [
        {
          name: this.$t('UI_Tab_Invite'),
          comp: 'lnvite-comp'
        },
        {
          name: this.$t('UI_Tab_Refer_Refer'),
          comp: 'referir-comp'
        },
        {
          name: this.$t('UI_Tab_Bonus'),
          comp: 'bonus-comp'
        },
        {
          name: this.$t('UI_Recomendar_Data_Hindi'),
          comp: 'date-comp',
          hidden: !this.isAgentDateSwitch
        },
        {
          name: this.$t('UI_Tab_Member'),
          comp: 'member-comp'
        }
      ].filter(f => !f.hidden)
    }
  },
  watch: {
    isApp() {
      this.rendering = false
      setTimeout(() => {
        this.rendering = true
      }, 10)
    }
  },
  created() {
    const { tab = 0 } = this.$route.query
    this.tabIndex = tab

    // 请求全民代红点
    this.$store.dispatch('base/showRedDotReceivedFun', {
      language: this.languageType,
      token: this.user.token,
      version: 2,
      event: 'user_spread_way'
    })
  },
  methods: {
    // 切换tab返回顶部
    onChange() {
      document.documentElement.scrollTop = 0
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.page.recomendar {
  .head_tab {
    top: $recomendar_tab_flex_top;
    left: $recomendar_tab_flex_left;
    right: $recomendar_tab_flex_right;
    z-index: 120;
    background: $recomendar_tab_bg;

    .tabs {
      .v-tab {
        // min-width: $recomendar_tab_min_width !important;
      }

      .tab_item_text {
        font-size: 12px;
        text-transform: $recomendar_tab_text_transform;
      }
      ::v-deep .v-slide-group__prev,
      ::v-deep .v-slide-group__next {
        display: none !important;
      }

      ::v-deep .tab_item.v-tab {
        // width: $recomendar_tab_width !important;
        height: $recomendar_tab_height !important;
        color: $recomendar_tab_color !important;
        padding: $recomendar_tab_padding !important;
      }

      ::v-deep .tab_item.v-tab.v-tab--active {
        color: $recomendar_tab_active_color !important;
      }

      .theme--dark.v-divider {
        border-color: $recomendar_tab_hr;
      }
    }
  }
  .conent {
    &::v-deep {
      .v-tabs-items {
        background: none !important;
      }
    }
  }
}

@media (max-width: 1080px) {
  .page.recomendar {
    width: $recomendar_page_app_width;
    padding: $recomendar_container_app_padding;

    .head_tab {
      position: $recomendar_tab_app_flex;
      height: $recomendar_tab_app_height;

      .tabs {
        width: $recomendar_page_app_width;
        padding: $recomendar_tab_app_padding;

        .tab_tify {
          padding: $recomendar_tab_tify_app_padding;
        }
        .tab_item_text {
          font-size: 10px;
        }
      }
    }

    .conent {
      margin: $recomendar_content_app_margin;
    }
  }
}

@media (min-width: 1080px) {
  .page.recomendar {
    width: $recomendar_page_pc_width;
    padding: $recomendar_container_pc_padding;

    .head_tab {
      position: $recomendar_tab_pc_flex;
      height: $recomendar_tab_pc_height;
      @include setBox(row, center, center);
      .tabs {
        width: $recomendar_page_pc_width;
        padding: $recomendar_tab_pc_padding;
        .tab_tify {
          padding: $recomendar_tab_tify_pc_padding;
        }
      }
    }

    .conent {
      margin: $recomendar_content_pc_margin;
    }
  }
}
</style>
