<template>
  <div class="comp Lnvite">
    <space-ui v-if="skeletonStatus" sign="recomendarLnvite" />
    <template v-else>
      <v-sheet color="t_card_bg" class="qrcode_card radius">
        <div class="link_bg">
          <div v-if="isQrcode" class="lt">
            <div ref="QRCrodeRef" class="code"></div>
            <div class="btn_code line_1 btn_radius" @click="downloadIamge">
              <div class="btn_code_text">
                <auto-font
                  uuid="inviteDownload"
                  :maxFont="
                    commonStyle(
                      isApp
                        ? 'invite_qrcode_card_lt_btn_code_text_app_size'
                        : 'invite_qrcode_card_lt_btn_code_text_pc_size'
                    )
                  "
                  :text="$t('UI_Invite_Download')"
                />
              </div>
            </div>
          </div>
          <div class="rt">
            <div class="link_title line_1">{{ $t('UI_Recommendar.referencia') }}</div>
            <div class="link_copy">
              <div class="link-input">{{ shareData.link }}</div>
              <icon-font
                v-zoom
                class="copy_btn cursor_point"
                name="icon-icon_account_copy"
                :size="iconSize.btn_icon_size"
                @click="onCopy"
              />
            </div>
            <div v-if="shareLinkArr?.length" class="link_copy_sign line_1">{{ $t('UI_Recommendar.SharaVia') }}</div>
            <div v-if="shareLinkArr?.length" class="link_copy_icon flex_row flex_row_wrap">
              <icon-font
                v-for="icon in shareLinkArr"
                :key="icon.id"
                v-zoom
                class="party cursor_point"
                :name="icon.icon"
                :size="iconSize.party_icon_size"
                @click="onShareLink(icon.sign)"
              />
            </div>
          </div>
        </div>
      </v-sheet>

      <v-sheet v-if="moneyCard.card" color="t_card_bg" class="money_card radius">
        <div class="total_ganho">
          <div class="total_ganho_top">
            <div class="lt">
              <span class="text">{{ $t('UI_Bouns') }}</span>
              <span class="amount_nub">
                &nbsp;{{ currencySymbol + $u.twoDigit(shareData.agent_commission || 0) }}
              </span>
            </div>
            <div class="rt">
              <div v-if="moneyCard.withdrawal && !isWithdrawShow">
                <v-btn
                  v-zoom="!isDisabled"
                  class="withdrawal_btn cursor_point btn_radius"
                  :class="{ disabled_btn: isDisabled }"
                  :color="iconSize.withdrawal_btn_color"
                  @click="onWithdraw"
                >
                  <auto-font uuid="retirarIds" :maxFont="12" :level="2" :text="$t('UI_Btn_Retirar')" />
                </v-btn>
              </div>

              <div class="lnvite_box">
                <v-btn
                  v-zoom="!isDisabled"
                  class="lnvite_btn cursor_point btn_radius"
                  :class="{ disabled_btn: isDisabled }"
                  :color="iconSize.lnvite_btn"
                  @click="onDeposit"
                >
                  <auto-font uuid="makeMoneyIds" :maxFont="12" :level="2" :text="$t('UI.MakeMoney_0_main_17')" />
                </v-btn>
                <template v-if="shareData.agent_commission_reward_ratio">
                  <div class="lnvite_give">+{{ shareData.agent_commission_reward_ratio }}%</div>
                </template>
              </div>
            </div>
          </div>
          <div class="total_ganho_bottom">
            {{ $t('UI_Commission') }}
          </div>
        </div>
      </v-sheet>

      <v-sheet v-if="shareData.telegram_btnname != undefined" color="t_card_bg" class="telegram_card radius">
        <div class="telegram_text break_word" v-html="telegramText"></div>
        <v-btn
          v-zoom
          class="telegram_btn cursor_point btn_radius"
          :color="iconSize.telegram_btn"
          @click="onTelegramBtn"
        >
          {{ shareData.telegram_btnname }}
        </v-btn>
      </v-sheet>

      <!-- <v-sheet color="t_card_bg" class="mt_16" :class="isApp ? 'radius_app' : 'radius_pc'">
        <div class="info_sheet" :class="isApp ? 'px_16 py_24' : 'px_40 py_24'">
          <div class="tt pr_24">
            <div class="lt font_20 font_600">
              <span class="amount_view">{{ currencySymbol }}</span>
              <span class="amount_nub">&nbsp;{{ $u.twoDigit(shareData.total_cash) }}</span>
            </div>
            <div class="rt mt_24 font_16">{{ $t('UI_Recommendar.total_cash') }}</div>
          </div>
          <v-divider vertical></v-divider>
          <div class="bt pl_24">
            <div class="lt font_20 font_600">{{ shareData.direct_num || 0 }}</div>
            <div class="rt mt_24 font_16">{{ $t('UI_Recommendar.direct_num') }}</div>
          </div>
        </div>
      </v-sheet> -->

      <!-- <v-sheet color="t_card_bg" class="mt_16" :class="isApp ? 'radius_app' : 'radius_pc'">
        <div class="hoje_table">
          <v-row class="mt_10 pl_20 pr_24 pt_20 pb_10" no-gutters>
            <v-col :cols="6" align="start">
              <div class="font_16 left_view">{{ $t('UI_Recommendar.share_box') }}</div>
            </v-col>
            <v-col :cols="6" align="end">
              <div class="font_16 rigth_view">{{ currencySymbol }}{{ $u.twoDigit(shareData.share_box) }}</div>
            </v-col>
          </v-row>
          <v-row class="pl_20 pr_24 pb_10" no-gutters>
            <v-col :cols="6" align="start">
              <div class="font_16 left_view">{{ $t('UI_Recommendar.sub_first_recharge') }}</div>
            </v-col>
            <v-col :cols="6" align="end">
              <div class="font_16 rigth_view">{{ currencySymbol }}{{ $u.twoDigit(shareData.sub_first_recharge) }}</div>
            </v-col>
          </v-row>
          <v-row class="pl_20 pr_24" no-gutters>
            <v-col :cols="6" align="start">
              <div class="font_16 left_view">{{ $t('UI_Recommendar.sub_cash') }}</div>
            </v-col>
            <v-col :cols="6" align="end">
              <div class="font_16 rigth_view">{{ currencySymbol }}{{ $u.twoDigit(shareData.sub_cash) }}</div>
            </v-col>
          </v-row>
        </div>
      </v-sheet> -->

      <v-sheet
        v-if="Object.keys(speedDatan).length > 0"
        id="moneySignCardId"
        color="t_card_bg"
        class="money_sign_card radius"
      >
        <div class="money_sign_card_box">
          <div class="money_sign_card_box_img">
            <img :src="getCacheIcon('refer_reward')" alt="" />
          </div>
          <div class="retorno_box_tip">
            <div class="retorno_box_title">{{ speedDatan.title || '' }}</div>
            <div v-if="isApp" class="retorno_box_title_img">
              <img :src="getCacheIcon('refer_reward')" alt="" />
            </div>
            <div class="retorno_box_text" v-html="contentText"></div>
          </div>
        </div>
        <div v-if="speedDatan.link > 0" class="retorno_box_btn cursor_point" @click="likeClick(speedDatan)">
          {{ $t('UI_Learn_More') }}
        </div>
      </v-sheet>

      <v-sheet v-if="isChestCard" id="chestCardId" color="t_card_bg" class="chest_card radius">
        <div class="progress_box_chest">
          <div class="progress_box_chest_title">{{ speedDatat.title || '' }}</div>
          <div class="progress_box_speed_box">
            <div v-zoom class="box_icon" @click="onBenefits">
              <icon-font
                class="cursor_point"
                name="icon-Icon_question"
                :dark="iconSize.box_icon_dark"
                :size="iconSize.box_icon_size"
              />
            </div>
            <div class="box_box">
              <spread-progress :total="shareData.share_count" :box-list="list" />
            </div>
          </div>
          <div class="progress_box_chest_content text_center">
            {{ $t('UI_Become', { X: shareData.game_count || 0 }) }}
          </div>
          <div class="progress_box_chest_cont text_center">{{ speedDatat.content }}</div>
          <div v-if="speedDatat.link > 0" class="progress_box_chest_btn cursor_point" @click="likeClick(speedDatat)">
            {{ $t('UI_Learn_More') }}
          </div>
        </div>
      </v-sheet>

      <!-- 新宝箱模块 -->
      <v-sheet v-if="newBoxInfo?.activity" id="newBoxCardId" color="t_card_bg" class="new_box_card radius">
        <div class="box-container">
          <div class="title">{{ newBoxInfo?.activity?.title }}</div>
          <div class="box-progress-bar">
            <swiper id="new-box-swiper" ref="swiperRef" :options="swiperOptionsInfo">
              <swiper-slide class="item location">
                <div class="location-image"></div>
              </swiper-slide>
              <swiper-slide v-for="(item, index) in formatNexBoxList" :key="index" class="item box">
                <div class="box-progress-warp">
                  <div class="progress" :style="{ width: item.processWidth }"></div>
                </div>
                <div class="box-wrap" @click="newBoxOpen(item)">
                  <div class="price">{{ currencySymbol }}{{ item.send_gold }}</div>
                  <div class="box-image">
                    <div v-show="item.status == 1" class="bg"></div>
                    <div class="default" :class="{ open: item.status == 2, 'opening glowing': item.status == 1 }"></div>
                  </div>
                  <div class="task" :class="{ reach: item.cur_num >= item.level_num }">
                    <span class="curr">{{ item.cur_num }}</span>
                    /
                    <span class="reach">{{ item.level_num }}</span>
                  </div>
                </div>
              </swiper-slide>
              <div slot="scrollbar" class="swiper-scrollbar" :class="{ 'd-block': isApp }"></div>
            </swiper>
            <div v-if="false" class="swiper-shade"></div>
          </div>
          <div class="description">
            <div
              ref="contentRef"
              class="content"
              :class="{ 'no-more': newBoxContentMore }"
              v-html="newBoxInfo?.activity?.content"
            ></div>
            <div v-if="newBoxContentMore" class="more" @click="newBoxContentMore = false">
              <span class="btn">{{ $t('UI_Learn_More') }}</span>
            </div>
          </div>
        </div>
      </v-sheet>

      <v-sheet color="t_card_bg" class="share_card radius">
        <div class="course_box">
          <div class="course_box_title">
            {{ speedDataf.title || '' }}
          </div>
          <div class="course_box_tip">
            {{ $t('UI_lncomeModel') }}
          </div>
          <div class="course_box_img c_box">
            <div v-image="'img_people'" class="course_box_img_box" style="width: 100%"></div>
            <icon-font v-if="!isApp" class="share_card_icon_img" name="icon-icon_email_chip" size="70" />
          </div>
          <!--分享奖励进度条模块 -->
          <inviteProgress />
          <div class="course_box_content text_center">
            {{ speedDataf.content || '' }}
          </div>
          <div v-if="speedDataf.link > 0" class="course_box_btn cursor_point" @click="likeClick(speedDataf)">
            {{ $t('UI_Learn_More') }}
          </div>
        </div>
        <icon-font v-if="!isApp" class="share_card_icon_left" name="icon-icon_email_chip" size="60" />
        <icon-font v-if="!isApp" class="share_card_icon_rigth" name="icon-icon_email_chip" size="80" />
      </v-sheet>

      <template v-if="shareData.agent_cash_desc">
        <v-sheet color="t_card_bg" class="desc_card radius">
          <div class="dispose_content" :class="isApp ? 'dispose_content_app' : 'dispose_content_pc'">
            <div class="dispose_content_title">{{ $t('UI_Common_Problem') }}</div>
            <div class="dispose_content_desc" v-html="descInfo"></div>
          </div>
        </v-sheet>
      </template>

      <!-- 接受金额弹窗 -->
      <invite-benefits ref="benefitsRef" />

      <!-- 绑定手机号 -->
      <custom-dialog v-model="phoneState">
        <bind-info
          class="dialog_radius"
          bind-data="phone"
          :title="$t('UI_recomendar_withdrawal_phoneTitle')"
          @submit="init"
          @back="phoneState = false"
        />
      </custom-dialog>
    </template>

    <a id="downloadQRCODE"></a>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import QRCode from 'qrcodejs2'
import { mapGetters, mapState } from 'vuex'
import copy from 'copy-to-clipboard'
import {
  spreadWay,
  agentReport,
  getCommissionDescList,
  agentCashTransfer,
  userShareStatistics,
  getInviteRewardBoxList,
  editAInviteRewardBox
} from '@/api/modules/agent.js'
import {
  FACEBOOK_SHARE,
  WHATSAPP_SHARE,
  SPREAD_NODE_ACCOUNTS,
  LINK_SHARE,
  TWITTER_SHARE,
  TG_SHARE
} from '@/api/setting'
import { jump } from '@/utils/linkMap'
import PublishSubscribe from 'publish-subscribe-js'
import SpreadProgress from './ui/spread-progress'
import InviteBenefits from './ui/invite-benefits'
import InviteProgress from './ui/invite-progress'
import bindInfo from '@/components/ui/bind-info'
import spaceUi from '@/components/skeleton/space-ui.vue'
import { callJava, isNativeClient, getEeplace } from '@/utils/helper'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/views/recomendar.modules.scss'

export default {
  components: {
    SpreadProgress,
    InviteBenefits,
    spaceUi,
    bindInfo,
    InviteProgress,
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      shareData: {}, // 分享数据
      recordState: false,
      phoneState: false, // 绑定手机弹窗
      list: [], // 宝箱进度
      speedDatan: {
        title: '',
        content: '',
        link: 0
      },
      speedDatat: {
        title: '',
        content: '',
        link: 0
      },
      speedDataf: {
        title: '',
        content: '',
        link: 0
      },
      timer: null,
      skeletonStatus: true,
      qrcode: '', // 分享二维码
      newBoxInfo: {}, // 新版宝箱数据
      newBoxList: [], // 新版宝箱列表
      newBoxContentMore: false, // 新版宝箱更多内容
      swiperOptionsInfo: {
        freeMode: true,
        freeModeMomentumRatio: 0.5,
        resistanceRatio: 0.1,
        slidesPerView: 'auto',
        spaceBetween: 0,
        scrollbar: {
          el: '.swiper-scrollbar',
          hide: true,
          draggable: true
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['user', 'languageType', 'systemConfig', 'isApp']),
    ...mapState('source', ['withdrawShare']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['isWithdrawShow']),
    // 外链共享权限
    shareLinkArr() {
      // share_app = [1,2,3,4]
      const { share_app = [] } = this.systemConfig
      // 映射列表
      const shareLink = [
        {
          id: '1',
          icon: 'icon-icon_refer_whatsapp',
          sign: 'whatsapp'
        },
        {
          id: '2',
          icon: 'icon-icon_refer_facebook',
          sign: 'facebook'
        },
        {
          id: '3',
          icon: 'icon-icon_refer_line',
          sign: 'line'
        },
        {
          id: '4',
          icon: 'icon-icon_refer_zalo',
          sign: 'zalo'
        },
        {
          id: '5',
          icon: 'icon-icon_TG',
          sign: 'TG'
        },
        {
          id: '6',
          icon: 'icon-icon_twitter',
          sign: 'twitter'
        }
      ]
      return shareLink
        .map(item => {
          if (share_app.includes(item.id)) return item
          return null
        })
        .filter(f => f)
    },
    // 处理邀请奖金富文本
    contentText() {
      return this.speedDatan.content ? getEeplace(this.speedDatan.content) : ''
    },
    // 处理金额类型
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 金额是否存在
    isDisabled() {
      return this.$u.twoDigit(this.shareData.agent_commission || 0) == 0
    },
    // 处理返回的信息
    descInfo() {
      const { agent_cash_desc = '' } = this.shareData
      if (!agent_cash_desc) return ''

      let info = agent_cash_desc.replace(/&lt;|&lt/g, '<')
      info = info.replace(/&quot;|&quot/g, '"')
      info = info.replace(/&gt;|&gt/g, '>')
      info = info.replace(/\n/g, '<br/>')

      return info
    },
    // 是否显示二维码
    isQrcode() {
      const { qrcode_open = false } = this.systemConfig
      return qrcode_open
    },
    // icon 大小
    iconSize() {
      const {
        invite_chest_card_progress_box_speed_box_icon_size,
        invite_qrcode_card_rt_link_copy_btn_icon_size,
        invite_qrcode_card_rt_link_sign_party_icon_size_app,
        invite_qrcode_card_rt_link_sign_party_icon_size_pc,
        invite_chest_card_progress_box_speed_box_icon_dark,
        invite_money_card_ganho_title_rt_withdrawal_btn_color,
        invite_money_card_ganho_title_rt_lnvite_btn_color,
        invite_qrcode_card_rt_telegram_btn_color
      } = _common

      return {
        box_icon_size: invite_chest_card_progress_box_speed_box_icon_size,
        btn_icon_size: invite_qrcode_card_rt_link_copy_btn_icon_size,
        party_icon_size: this.isApp
          ? invite_qrcode_card_rt_link_sign_party_icon_size_app
          : invite_qrcode_card_rt_link_sign_party_icon_size_pc,
        box_icon_dark: invite_chest_card_progress_box_speed_box_icon_dark,
        withdrawal_btn_color: invite_money_card_ganho_title_rt_withdrawal_btn_color,
        lnvite_btn: invite_money_card_ganho_title_rt_lnvite_btn_color,
        telegram_btn: invite_qrcode_card_rt_telegram_btn_color
      }
    },
    // 宝箱模块是否显示
    isChestCard() {
      const { agent_share_reward_switch } = this.shareData
      return agent_share_reward_switch == 1
    },
    // 处理直推富文本
    telegramText() {
      return this.shareData.telegram_show_text ? getEeplace(this.shareData.telegram_show_text) : ''
    },
    /**
     * 推广金额模块是否显示，提现按钮是否显示
     * card: 模块是否显示
     * withdrawal: 提现按钮是否显示
     */
    moneyCard() {
      /**
       * 0关闭这个功能（全部显示），1为开启这个功能（全部隐藏），2为只隐藏全民代提现按钮（显示主模块，隐藏提现按钮）
       */
      let { agent_auto_transfer = 0 } = this.shareData
      agent_auto_transfer = Number(agent_auto_transfer)

      if (agent_auto_transfer == 0) return { card: true, withdrawal: true }
      if (agent_auto_transfer == 1) return { card: false, withdrawal: false }
      if (agent_auto_transfer == 2) return { card: true, withdrawal: false }

      return {}
    },
    // 新宝箱模块列表数据格式化计算
    formatNexBoxList() {
      if (this.newBoxList.length < 1) {
        return []
      }
      this.newBoxList.map((item, index) => {
        // 进度条完成状态
        if (item.cur_num >= item.level_num) {
          item.processWidth = '100%'
        } else if (index > 0 && item.cur_num >= this.newBoxList[index - 1].level_num) {
          //进度条中间状态
          item.processWidth = '50%'
        } else {
          item.processWidth = '0%'
        }
        return item
      })
      return this.newBoxList
    }
  },
  watch: {
    // 全民代是否提现成功 -- 更新全民代展示信息
    withdrawShare() {
      this.init()
    },
    languageType() {
      this.init()
    }
  },
  async created() {
    await this.init()
    this.scrollToBox()
  },
  methods: {
    commonStyle: k => _common[k],
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    // 初始化
    /**
     * 1.spreadWay 接口 可领取金额 + 分享链接地址
     * 2.agentReport  宝箱数据,
     * 3."share_box": 0, //邀请宝箱
     *   "sub_cash": 0, //推广业绩奖金
     *   "sub_first_recharge": 0,//首充返现
     *   "direct_num": 0, //一级用户数量
     *    sub_total_users  下级首充人数
     **/
    async init() {
      let state = true
      const params = { token: this.user.token, language: this.languageType }
      const par = {
        token: this.user.token,
        language: this.languageType,
        start_date: this.$u.getFormatDate({ date: new Date() }),
        end_date: this.$u.getFormatDate({ date: new Date() })
      }
      const commParams = {
        token: this.user.token,
        language: this.languageType
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const [spreadRes, agentRes, commissionDescRes, newBoxRes] = await Promise.all([
        spreadWay(params).catch(e => e),
        agentReport(par).catch(e => e),
        getCommissionDescList(commParams).catch(e => e),
        getInviteRewardBoxList(commParams).catch(e => e)
      ])
      state = false
      if (this.timer) clearTimeout(this.timer) // 清除
      this.$pop.loading().hide()

      const { code: spreadCode, data: spreadData = {} } = spreadRes
      if (spreadCode == 0) this.shareData = { ...this.shareData, ...spreadData }

      const { code: agentCode, data: agentData = {} } = agentRes
      if (agentCode == 0) this.shareData = { ...this.shareData, ...agentData }
      const { code: commissionDescCode, data: commissionDescData = [] } = commissionDescRes
      const { code: newBoxCode, data: newBoxResData = [] } = newBoxRes
      const formatCommission = commissionDescData.reduce((acc, cur) => {
        return {
          ...acc,
          [cur.mark]: cur
        }
      }, {})
      if (commissionDescCode == 0) this.shareData = { ...this.shareData, ...formatCommission }
      // 新宝箱数据
      if (newBoxCode == 0) {
        this.newBoxList = newBoxResData?.list.map(item => {
          return item
        })
        this.newBoxInfo = newBoxResData
      }
      this.initPage()
      this.$nextTick(() => {
        this.createQrcode()
        this.newBoxContentOverflow()
      })
    },
    // 复制到剪贴板
    onCopy() {
      const { share_content = '', link = '' } = this.shareData
      const isCopy = copy(share_content + '       ' + link)
      if (isCopy) {
        this.$pop.message().show({ content: this.$t('UI_Copied') })
        // 上报打点统计
        userShareStatistics({ token: this.user.token })
      }
    },
    /**
     * 打开共享外链
     * @param {String} sign 标识
     */
    onShareLink(sign = '') {
      if (!sign) return
      const { share_content = '', link = '' } = this.shareData
      switch (sign) {
        case 'whatsapp':
          open(`${WHATSAPP_SHARE}?text=${share_content}    ${link}`)
          break
        case 'facebook':
          open(`${FACEBOOK_SHARE}?u=${link}&content=${share_content}`)
          break
        case 'line':
          if (isNativeClient()) {
            callJava('LineAppShareUrl', { url: share_content + '    ' + link })
          } else {
            open(`${LINK_SHARE}?text=${share_content}&url=${encodeURIComponent(link)}`)
          }
          break
        case 'zalo':
          if (isNativeClient()) callJava('zaloShareCallback', { msg: share_content, link })
          break
        case 'TG':
          open(`${TG_SHARE}?text=${encodeURIComponent(share_content)}&url=${encodeURIComponent(link)}`)
          break
        case 'twitter':
          open(`${TWITTER_SHARE}?text=${encodeURIComponent(share_content)}&url=${encodeURIComponent(link)}`)
          break
        default:
          break
      }
    },
    // 提现
    async onWithdraw() {
      if (this.isDisabled) return
      if (this.shareData.transfer_limit)
        return this.$pop.message().show({ content: this.shareData.transfer_limit_desc })
      // 是否开启绑定手机号 agent_withdraw_need_tel 0关 1开
      const { agent_withdraw_need_tel = 0 } = this.shareData
      if (agent_withdraw_need_tel == 1) {
        this.phoneState = true
      } else {
        this.$store.dispatch('modal/setDepositWithdrawShow', { tab: 'withdraw', entrance: 1 })
      }
    },
    // 接受金额
    onDeposit() {
      if (this.isDisabled) return
      if (this.shareData.transfer_limit)
        return this.$pop.message().show({ content: this.shareData.transfer_limit_desc })

      const { agent_commission = 0, agent_commission_reward_ratio = 0 } = this.shareData
      const oldGold = this.$u.twoDigit(agent_commission)
      const tional = this.$u.twoDigit(Math.floor(agent_commission * (agent_commission_reward_ratio / 100)))
      const newGold = this.$u.twoDigit(Number(agent_commission) + Number(tional))
      const giveText = `
                                <div style="
                                    width: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_width'
                                        : 'invite_confirm_give_text_pc_width'
                                    )};
                                    height: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_height'
                                        : 'invite_confirm_give_text_pc_height'
                                    )};
                                    margin: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_margin'
                                        : 'invite_confirm_give_text_pc_margin'
                                    )};
                                    border-radius: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_radius'
                                        : 'invite_confirm_give_text_pc_radius'
                                    )};
                                    border: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_border'
                                        : 'invite_confirm_give_text_pc_border'
                                    )};
                                    font-size: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_size'
                                        : 'invite_confirm_give_text_pc_size'
                                    )};
                                    font-weight: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_weight'
                                        : 'invite_confirm_give_text_pc_weight'
                                    )};
                                    color: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_color'
                                        : 'invite_confirm_give_text_pc_color'
                                    )};
                                    line-height: ${this.commonStyle(
                                      this.isApp
                                        ? 'invite_confirm_give_text_app_line'
                                        : 'invite_confirm_give_text_pc_line'
                                    )};
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;"
                                >
                                    ${this.$t('UI_Recomendar_Give_Tip')} = ${newGold}(${oldGold} + ${tional})
                                </div>
                            `

      this.$pop
        .confirm({
          confirmText: this.$t('General.General_1'),
          cancelText: this.$t('General.General_2'),
          content: `${this.$t('UI_Recomendar_chipmsg')}${agent_commission_reward_ratio ? giveText : ''}`,
          success: async () => {
            const params = {
              token: this.user.token,
              language: this.languageType
            }
            const { code, msg } = await agentCashTransfer(params).catch(e => e)
            if (code != 0) return this.$pop.message().show({ content: msg })

            // 更新红点信息
            this.$store.dispatch('base/showRedDotReceivedFun', {
              version: 2,
              event: 'user_spread_way',
              language: this.languageType,
              token: this.user.token
            })

            // 插入弹窗序列
            PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
              key: 'deposit',
              seat: 'first',
              params: {
                title: this.$t('UI_Code_Bonus'),
                value: Number(agent_commission) + Number(tional)
              }
            })

            this.init()
          }
        })
        .show()
    },
    // 宝箱记录
    onBenefits() {
      if (!this.$refs['benefitsRef']) return
      this.$refs['benefitsRef'].open()
    },
    // 宝箱初始化
    initPage() {
      /**
       * 规则：根据share_count , share_list[] count 关系获取索引，向后取四个
       */
      const {
        share_count,
        share_list = [],
        1: speedData0 = {},
        2: speedData1 = {},
        3: speedData2 = {}
      } = JSON.parse(JSON.stringify(this.shareData))
      this.speedDatan = speedData0
      this.speedDatat = speedData1
      this.speedDataf = speedData2
      if (share_list && Array.isArray(share_list) && share_list.length > 0) {
        const len = share_list.length
        // 不足四个
        if (len <= SPREAD_NODE_ACCOUNTS) {
          this.list = share_list
          this.skeletonStatus = false
          return
        }
        let startIndex, endIndex
        startIndex = share_list.findIndex(item => item.count > share_count)
        endIndex = startIndex + SPREAD_NODE_ACCOUNTS
        // 全部完成取后四个 || 超出边界 取后四个
        if (startIndex < 0 || endIndex > len - 1) {
          this.list = share_list.slice(-SPREAD_NODE_ACCOUNTS)
          this.skeletonStatus = false
          return
        }
        this.list = share_list.slice(startIndex, endIndex)
      }
      this.skeletonStatus = false
    },
    likeClick(item) {
      jump(item, this)
    },
    // 生成分享二维码
    createQrcode() {
      if (!this.$refs['QRCrodeRef']) return
      // 有新的二维码地址了，先把之前的清除掉
      if (this.qrcode) this.$refs['QRCrodeRef'].innerHTML = ''

      const { share_content = '', link = '' } = this.shareData
      this.qrcode = new QRCode(this.$refs['QRCrodeRef'], {
        id: 'qrCrodeId',
        text: share_content + '       ' + link,
        width: parseInt(
          this.commonStyle(
            this.isApp ? 'invite_qrcode_card_lt_qr_code_app_width' : 'invite_qrcode_card_lt_qr_code_pc_width'
          )
        ),
        height: parseInt(
          this.commonStyle(
            this.isApp ? 'invite_qrcode_card_lt_qr_code_app_height' : 'invite_qrcode_card_lt_qr_code_pc_height'
          )
        ),
        correctLevel: QRCode.CorrectLevel.Q //纠错码 L:1,M:0,Q:3,H:2
      })
    },
    // 下载二维码
    downloadIamge() {
      const nodeList = Array.prototype.slice.call(this.qrcode?._el?.children || [])
      const canvas = nodeList[0]
      if (canvas && canvas.nodeName && canvas.nodeName.toLocaleLowerCase() == 'canvas') {
        const url = canvas.toDataURL('image/png')
        document.getElementById('downloadQRCODE').setAttribute('href', url)
        document.getElementById('downloadQRCODE').setAttribute('download', `${new Date().getTime()}.png`)
        document.getElementById('downloadQRCODE').click()

        this.$pop.message().show({ content: this.$t('UI_Download_Iamge') })
        if (isNativeClient()) callJava('saveQR', { str: url }, new Date().getTime())
      }
    },
    // 直推按钮
    onTelegramBtn() {
      if (this.shareData.telegram_chat_uri) window.open(this.shareData.telegram_chat_uri)
    },
    // 宝箱打开
    async newBoxOpen(item) {
      if (item.status != 1) {
        return
      }
      const res = await editAInviteRewardBox({
        token: this.user?.token,
        language: this.languageType,
        uid: this.user?.uid,
        id: item.id
      }).catch(e => e)
      if (res?.code != 0) return this.$pop.message().show({ content: res?.msg })
      item.status = 2

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Code_Bonus'),
          value: Number(item.send_gold) || 0
        }
      })
      this.refreshDigit()
    },
    // 新宝箱内容超出判断
    newBoxContentOverflow() {
      const line = 3 // 显示行数
      const element = this.$refs['contentRef']
      const text = element.textContent
      const tempElement = document.createElement('span')
      tempElement.style.display = 'inline-block'
      tempElement.style.width = 'max-content'
      tempElement.textContent = text
      document.body.appendChild(tempElement)
      const width = tempElement.offsetWidth
      document.body.removeChild(tempElement)
      this.newBoxContentMore = width / line > element.offsetWidth
    },
    // 刷新金币
    refreshDigit() {
      this.$store.commit('source/SET_IS_ROTATE', true)
      this.$store.dispatch('base/refreshAccount', () => {
        this.$store.commit('source/SET_IS_ROTATE', false)
      })
    },
    // 新宝箱滚动到已打开宝箱最后一个
    scrollToBox() {
      const lastOpeningBox = this.newBoxList.findIndex(item => item.status === 1)
      const lastUnOpenBox = this.newBoxList.findLastIndex(item => item.status === 2)
      // 有待打开宝箱滚动到第一个 || 已打开最后一个滚动到第一个
      let scrollIndex = lastOpeningBox != -1 ? lastOpeningBox : lastUnOpenBox
      this.$nextTick(() => {
        if (this?.$refs['swiperRef']?.$swiper) {
          this.$refs['swiperRef'].$swiper.slideTo(scrollIndex + 1)
        }
      })
    }
  }
}
</script>

