      }

      .progress_box_chest_btn {
        font-size: $invite_chest_card_progress_box_chest_btn_size;
        font-weight: $invite_chest_card_progress_box_chest_btn_bold;
        margin: $invite_chest_card_progress_box_chest_btn_margin;
        color: $invite_chest_card_progress_box_chest_btn_color;
        text-decoration: $invite_chest_card_progress_box_chest_btn_decoration;
      }
    }
  }

  .share_card {
    margin: $invite_share_card_margin;
    background-color: $invite_shaer_card_bg;
    position: relative;
    overflow: hidden;
    .course_box {
      padding: $invite_share_card_course_box_padding;
      @include setBox(column, center, center);

      .course_box_title {
        font-size: $invite_share_card_course_box_course_box_title_size;
        font-weight: $invite_share_card_course_box_course_box_title_bold;
        text-align: $invite_share_card_course_box_course_box_title_align;
        color: $invite_share_card_course_box_course_box_title_color;
        line-height: 1.2;
        @include set_text_ellipsis(2);
        padding: $invite_share_card_course_box_course_box_title_app_padding;
      }

      .course_box_tip {
        width: $invite_share_card_course_box_course_box_tip_width;
        font-size: $invite_share_card_course_box_course_box_tip_size;
        font-weight: $invite_share_card_course_box_course_box_tip_bold;
        margin: $invite_share_card_course_box_course_box_tip_margin;
        text-align: $invite_share_card_course_box_course_box_tip_align;
        color: $invite_share_card_course_box_course_box_tip_color;
        padding: $invite_share_card_course_box_course_box_tip_app_padding;
      }

      .course_box_content {
        color: $invite_share_card_course_box_course_box_content_color;
        margin: $invite_share_card_course_box_course_box_content_margin;
        font-size: $invite_share_card_course_box_course_box_content_size;
        font-weight: $invite_share_card_course_box_course_box_content_bold;
        padding: $invite_share_card_course_box_course_box_content_app_padding;
      }
      // .invite_progress_view {
      //     padding: 0px 22px;
      //     width: 100%;
      // }
      .course_box_btn {
        font-size: $invite_share_card_course_box_course_box_btn_size;
        font-weight: $invite_share_card_course_box_course_box_btn_bold;
        margin: $invite_share_card_course_box_course_box_btn_margin;
        color: $invite_share_card_course_box_course_box_btn_color;
        text-decoration: $invite_share_card_course_box_course_box_btn_decoration;
      }
    }
    .share_card_icon_left {
      position: absolute;
      bottom: 26%;
      left: -18px;
      z-index: 1;
      color: #374149;
      transform: translateY(-26%);
    }
    .share_card_icon_rigth {
      position: absolute;
      top: 50%;
      right: -26px;
      transform: translateY(-50%);
      z-index: 1;
      color: #374149;
    }
    .course_box_img {
      padding: $invite_share_card_course_box_img_app;
      .share_card_icon_left,
      .share_card_icon_rigth,
      .share_card_icon_img {
        display: none;
      }
    }
  }

  .desc_card {
    margin: $invite_desc_card_margin;
    background-color: $invite_desc_card_bg;
    .dispose_content {
      padding: $invite_desc_card_dispose_content_app_padding;

      .dispose_content_title {
        font-size: $invite_desc_card_dispose_content_dispose_content_title_size;
        font-weight: $invite_desc_card_dispose_content_dispose_content_title_bold;
        text-align: $invite_desc_card_dispose_content_dispose_content_title_align;
        color: $invite_desc_card_dispose_content_dispose_content_title_color;
        line-height: 1.2;
      }

      .dispose_content_desc {
        font-size: $invite_desc_card_dispose_content_dispose_content_desc_size;
        font-weight: $invite_desc_card_dispose_content_dispose_content_desc_bold;
        margin: $invite_desc_card_dispose_content_dispose_content_desc_margin;
        color: $invite_desc_card_dispose_content_dispose_content_desc_color;
      }
    }
  }

  .new_box_card {
    .box-container {
      padding: 10px;
      text-align: center;
      background: $new_box_container_bg;
      max-width: 1000px;
      margin: 50px auto;
      border-radius: 10px;
      .swiper-shade {
        width: 10%;
        height: 100%;
        background: linear-gradient(
          to right,
          rgba($color: #19212f, $alpha: 0.1),
          rgba($color: #19212f, $alpha: 0.8),
          rgba($color: #19212f, $alpha: 1)
        );
        position: absolute;
        top: 0;
        right: -10px;
        z-index: 2;
        filter: blur(10px);
      }
    }
    .box-container .title {
      font-size: 24px;
      font-weight: bold;
    }
    .title {
      font-size: 20px;
      margin-bottom: 30px;
      text-align: left;
    }

    .box-progress-bar {
      display: flex;
      position: relative;
      :deep(#new-box-swiper) {
        width: 100%;
        padding-bottom: 10px;
        .swiper-scrollbar {
          height: 2px;
        }
        .swiper-scrollbar-drag {
          background-color: rgba($color: #fff, $alpha: 0.5);
        }
      }
    }

    .item {
      text-align: center;
      position: relative;
      display: flex;
    }

    .location {
      position: relative;
      z-index: 2;
      width: 5%;
      flex-direction: column;
      justify-content: center;
      transform: scale(1.25);
    }
    .location-image {
      background-image: $new_box_location_image;
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: center center;
      cursor: pointer;
      height: 100%;
    }

    .price {
      font-size: 12px;
      color: $new_box_price_color;
      font-weight: bold;
    }
    .box {
      width: 23.75%;
      display: flex;
    }
    .box-progress-warp {
      position: relative;
      flex: 1;
      height: 2px;
      border-radius: 10px;
      align-self: center;
      background-color: $new_box_progress_warp_bg;
    }
    .box-progress-warp .progress {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      border-radius: 10px;
      align-self: center;
      background-color: $new_box_progress_warp_progress_bg;
    }
    .box-wrap {
      width: 60%;
      max-width: 60px;
    }
    .box-wrap .task {
      color: $new_box_warp_task_color;
      font-size: 14px;
    }
    .box-wrap .task.reach {
      color: $new_box_warp_task_reach_color;
    }
    .box-image {
      position: relative;
      width: 100%;
      padding-top: 100%;
      transition: transform 0.5s;
    }
    .box-image .bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: $new_box_pending_image;
      background-size: cover;
      cursor: pointer;
      animation: xz 5s linear infinite;
    }
    .box-image .default {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: $new_box_base_image;
      background-size: cover;
      cursor: pointer;
    }

    .box-image .glowing {
      animation: zy 2s 0.15s linear infinite;
    }
    .box-image .pending {
      background-image: $new_box_base_image;
      background-repeat: no-repeat;
      background-size: 100%;
    }

    .box-image .open {
      background-image: $new_box_open_image; /* 替换为实际打开宝箱图片 */
      transform: scale(1.1); /* 宝箱放大效果 */
    }
    .description {
      padding-top: 20px;
      text-align: left;
      line-height: 1.5;
      color: #bbcedf;
      .content {
        font-size: 14px;
        word-break: break-all;
        white-space: pre-wrap;
        &.no-more {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
      }
    }
    .description .more {
      text-align: center;
      span {
        display: inline-block;
        padding: 6px 14px;
        margin: 16px 0;
        font-size: 15px;
        border: 1px solid $new_box_more_border_color;
        background: $new_box_more_border_bg;
        border-radius: 6px;
        color: #fff;
      }
    }
    /* 旋转 */
    @keyframes xz {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    /* 摇晃 */
    @keyframes zy {
      10% {
        transform: rotate(15deg);
      }
      20% {
        transform: rotate(-10deg);
      }
      30% {
        transform: rotate(5deg);
      }
      40% {
        transform: rotate(-5deg);
      }
      50%,
      100% {
        transform: rotate(0deg);
      }
    }
  }
}

// pc 端兼容样式
@include breakpoint-only-up($px: 1080px) {
  .comp.Lnvite {
    .radius {
