          display: block;
        }

        .retorno_box_tip {
          @include setBox(column, center, center);
          padding: $invite_money_sign_card_retorno_box_tip_pc_padding;
        }
      }
      .retorno_box_btn {
        position: relative;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        padding: 0;
      }
    }
    .chest_card {
      .progress_box_chest {
        padding: $invite_chest_card_progress_box_chest_pc_padding;
      }
