<template>
  <div v-if="visible" v-zinx class="comp invite_benefits">
    <div class="wrapper-mask"></div>
    <div class="benefits" :style="`top: ${top};`">
      <div ref="benefitsBoxRef" class="benefits_box radius">
        <div class="benefits_box_title">
          <span class="line_1">{{ $t('UI_Share_InvitesBonus') }}</span>
          <icon-font
            class="close_icon cursor_point"
            name="icon-Icon_Cloose"
            :size="iconSize.colse_icon_size"
            dark="t_text_color_2"
            @click="close"
          />
        </div>
        <div class="benefits_box_scroll">
          <table-temp v-if="isTable" :table-head="tableHead" :table-list="tableList" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { spreadWay } from '@/api/modules/agent.js'
import { POP_TIME } from '@/api/setting'
import tableTemp from '@/components/ui/table-temp'
import _common from '@style/views/recomendar.modules.scss'

export default {
  name: 'InviteBenefits',
  components: {
    tableTemp
  },
  props: {
    top: {
      type: [Number, String],
      default: '20%'
    }
  },
  data() {
    return {
      visible: false, // 弹窗是否显示
      isTable: false, // 是否显示列表
      tableList: [] // 列表
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    ...mapState('switchState', ['currencyShow']),
    // 金额单位
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 关闭按钮样式
    iconSize() {
      const { benefits_colse_icon_size } = _common

      return {
        colse_icon_size: benefits_colse_icon_size
      }
    },
    // 表头
    tableHead() {
      const {
        benefits_table_size,
        benefits_table_bold,
        benefits_table_app_radius,
        benefits_table_pc_radius,
        benefits_table_zebra,
        benefits_table_is_scroll,
        benefits_table_head_bg,
        benefits_table_head_radius,
        benefits_table_head_text_color,
        benefits_table_head_size,
        benefits_table_head_bold,
        benefits_table_head_align,
        benefits_table_head_margin,
        benefits_table_head_padding_app,
        benefits_table_head_padding_pc,
        benefits_table_head_fixed,
        benefits_table_hor_bg,
        benefits_table_hor_app_padding,
        benefits_table_hor_pc_padding,
        benefits_table_hor_border_t,
        benefits_table_hor_border_r,
        benefits_table_hor_border_b,
        benefits_table_hor_border_l,
        benefits_table_hor_margin,
        benefits_table_hor_color,
        benefits_table_hor_size,
        benefits_table_hor_bold,
        benefits_table_hor_align
      } = _common

      return {
        size: {
          fontSize: benefits_table_size,
          fontWeight: benefits_table_bold
        },
        radius: this.isApp ? benefits_table_app_radius : benefits_table_pc_radius,
        zebra: benefits_table_zebra,
        isScroll: !(benefits_table_is_scroll == false + ''),
        head: {
          bg: benefits_table_head_bg,
          color: benefits_table_head_text_color,
          radius: benefits_table_head_radius,
          size: {
            fontSize: benefits_table_head_size,
            fontWeight: benefits_table_head_bold,
            textAlign: benefits_table_head_align
          },
          margin: benefits_table_head_margin,
          padding: this.isApp ? benefits_table_head_padding_app : benefits_table_head_padding_pc,
          fixed: !(benefits_table_head_fixed == false + '')
        },
        hor: {
          bg: benefits_table_hor_bg,
          padding: this.isApp ? benefits_table_hor_app_padding : benefits_table_hor_pc_padding,
          border_t: benefits_table_hor_border_t,
          border_r: benefits_table_hor_border_r,
          border_b: benefits_table_hor_border_b,
          border_l: benefits_table_hor_border_l,
          margin: benefits_table_hor_margin,
          color: benefits_table_hor_color,
          size: {
            fontSize: benefits_table_hor_size,
            fontWeight: benefits_table_hor_bold,
            textAlign: benefits_table_hor_align
          }
        },
        columns: [
          {
            label: this.$t('UI_Recomendar_spreadTips'),
            prop: 'count',
            width: '150px',
            align: 'center'
          },
          {
            label: this.$t('UI.MakeMoney_4_Promotion_3'),
            prop: 'gold',
            align: 'center'
          }
        ]
      }
    }
  },
  methods: {
    // 打开弹窗
    open() {
      this.visible = !this.visible
      this.getList()

      this.$nextTick(() => {
        const benefitsBoxRef = this.$refs['benefitsBoxRef']
        if (benefitsBoxRef) this.$anim.insert(benefitsBoxRef)

        setTimeout(() => {
          this.isTable = true
        }, POP_TIME)
      })
    },
    // 关闭弹窗
    close() {
      this.$nextTick(() => {
        const benefitsBoxRef = this.$refs['benefitsBoxRef']
        if (benefitsBoxRef) this.$anim.close(benefitsBoxRef)

        setTimeout(() => {
          this.visible = false
          this.isTable = false
        }, POP_TIME)
      })
    },
    // 获取列表
    async getList() {
      const params = { token: this.user.token, language: this.languageType }
      const { code, data } = await spreadWay(params).catch(e => e)
      if (code != 0) return

      const { share_list = [] } = data
      this.tableList = share_list.map((item, inx) => {
        return {
          count: item.count,
          gold: this.currencySymbol + ' ' + item.gold
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recomendar.modules.scss';

.radius {
  border-radius: $benefits_app_radius;
}

.comp.invite_benefits {
  .benefits {
    width: $benefits_page_app_width;
    position: fixed;
    left: $benefits_page_app_left;
  }

  .benefits_box {
    padding: $benefits_page_benefits_box_padding;
    background: $benefits_page_benefits_box_bg;
    @include setBox(column, center, center);

    .benefits_box_title {
      width: $benefits_page_benefits_box_title_width;
      font-size: $benefits_page_benefits_box_title_size;
      font-weight: $benefits_page_benefits_box_title_bold;
      position: relative;
      @include setBox(row, center, center);

      .close_icon {
        position: absolute;
        top: $benefits_page_benefits_box_title_icon_top;
        right: $benefits_page_benefits_box_title_icon_right;
        transform: translateY(-50%);
      }
    }

    .benefits_box_scroll {
      width: $benefits_page_benefits_box_table_width;
      height: $benefits_page_benefits_box_table_height;
      margin: $benefits_page_benefits_box_table_margin;
    }
  }
}
// pc样式
@include breakpoint-only-up() {
  .radius {
    border-radius: $benefits_pc_radius;
  }
  .comp.invite_benefits {
    .benefits {
      width: $benefits_page_pc_width;
      position: fixed;
      left: $benefits_page_pc_left;
      transform: translate(-50%, 0);
    }
  }
}
</style>
